import Vue from 'vue'
import Vuex from 'vuex'
import {appCookie} from '@/plugins/util'
import {getWebsiteList, getUserLoginState} from '@/data/service'
//import {getWebSites} from '@/datas/query'


Vue.use(Vuex)


export default new Vuex.Store({
    
    state: {
        website: appCookie('website') || 'hz', //分站标识
        pid: appCookie('pid'), //当前页面pid，男装30，女装16
        userId: '', //用户IDuserId
        userType: '', //用户类型：1分销商，2供应商

        websiteList: [],
        websiteMap: {},

        //加载loading
        loading:{
            status: false,
            //text:''
        }
        
    },
    
    mutations: {

        setWebsite(state, website){
            if(state.website == website){
                return
            }
            appCookie('website', state.website = website)
        },
        
        togglePageType(state){
            this.commit('setPageType', state.pid == 16 ? 'man' : 'woman')
        },
        setPageType(state, pageType){
            if(pageType == 'man'){
                state.pid = 30
            }else if(pageType == 'woman'){
                state.pid = 16
            }
            appCookie('pid', state.pid)
        },

        setUserInfo(state, payload){
            if(state.userId == payload.userId && state.userType == payload.userType){
                return
            }
            state.userId = payload.userId
            state.userType = payload.userType
        },

        // 修改loding状态
        updateLoadingStatus(state, status) {
            state.loading.status = status.status;
            state.loading.text = status.text;
        },
        
    },
    
    getters: {
        
        targetPageTypeName(state){
            return ({
                16: '男装',
                30: '女装',
            })[state.pid = state.pid || appCookie('pid') ]
        },
        
        targetWebsiteName(state){
            return state.websiteMap[state.website = state.website || appCookie('website')]
        },
        
        targetLoadingText(state){
            return state.loading.text = state.loading.text
        }
    },
    

    actions: {
        
        async initWebsite ({commit, state, dispatch}) {
            await dispatch('initWebsiteList')
            
            commit('setWebsite', appCookie('website') ? appCookie('website') : 'hz')
        },
        
        async initWebsiteList ({commit, state}) {

            if(state.websiteList.length){
                return;
            }
        
            let res = await getWebsiteList();
            if(res.success){
                state.websiteList =  res.sites.map(
                    website => ({ 
                        id: website.webSite, 
                        name: website.siteName, 
                    })
                )
                var websiteMap = {}
                state.websiteList.forEach(
                    website => {
                        websiteMap[website.id] = website.name
                    }
                )
                state.websiteMap = websiteMap
            }
         
        },

        //判断登录状态
        async initUserLoginState({commit, state}){
            let res = await getUserLoginState() 
            if(res.success && res.userId){
                commit('setUserInfo', {userId:res.userId, userType:res.userType})
            }
            
        },

        // 控制loading
        startLoding({state, commit}, status){
            commit('updateLoadingStatus', status)
        },
    }
    
})






