<template>
	<section>
        <div class="list_item" v-for="(item, index) in goods">
            <router-link :to="{name:'goods', params:{id:item.goodsid}}">
                <img :src="item.imgSrc">                    
                <div class="info">
                    <h3>{{item.title}}</h3>
                    <span class="price"><i>¥</i>{{item.price}}</span>
                    <span class="market" v-if="!showMarket">{{item.market}}</span>
                </div>
            </router-link>
        </div>
	</section>
</template>

<script>
export default{
	data(){
        return{
        }
    },
    props:['showMarket', 'goods']
}
</script>


<style scoped>
.list_item{padding-left:.12rem; margin-top:.14rem; overflow:hidden;}
.list_item:last-child{padding-bottom:.14rem;}
.list_item a{display:flex;}
.list_item img{width:.96rem; height:.96rem; margin-right:.12rem;}
.list_item .info{flex:1; border-bottom:1px solid #d8d8d8;}
.list_item .info h3{font-size:.12rem; color:#333; line-height:.2rem; padding-right:.06rem; overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:2;-webkit-box-orient:vertical; margin-bottom:.24rem}
.list_item .info .price{float:left; color:#f40; font-size:.16rem;}
.list_item .info .price i{font-size:.12rem; margin-right:.02rem;}
.list_item .info .market{float:right; color:#999; font-size:.12rem; margin-right:.08rem; position:relative; top:.04rem;}
</style>


