`<template>
    <div class="websiteListPanel">
        <common-head :view="{
            goback: true,
            pageTitle: '选择城市'
        }"></common-head>
        
        <ul class="websiteList">
			<li v-for="website in websiteList">
                <span @click="setWebsiteAndGoback(website.id)">
                    {{website.name}}
					<i class="iconfont icon-right"></i>
                </span>  
			</li>
		</ul>
        
    </div>
</template>


<script>

import {mapState, mapMutations, mapActions, mapGetters} from 'vuex'
import router from '@/router/index'
import commonHead from '@/components/commonHead'

export default {
    name: 'websiteListPanel',
    components: {
        commonHead
    },
    datas(){
        return {
        
        }
    },
    created(){
        this.initWebsiteList()
    },
    
    methods: {
        ...mapMutations([
            'setWebsite',
        ]),
        ...mapActions([
            'initWebsiteList',
        ]),
        setWebsiteAndGoback(website){
            this.setWebsite(website)
            router.go(-1)
        }
        
    },
    
    computed: {
        ...mapState([
            'websiteList',
        ]),
    }

}
</script>

<style scope>
.websiteList{width:100%; position:absolute; top:.58rem; background:#fff;}
.websiteList li{padding:.16rem 0; margin:0 .16rem; border-bottom:1px solid #d8d8d8; font-size:.16rem;}
.websiteList li span{display:block; color:#666;}
.websiteList li span i{float:right; color:#d8d8d8; font-size:.14rem; position:relative; top:.03rem;}
.websiteList li:last-child{border-bottom:0;}
</style>




