import Vue from 'vue'
import Router from 'vue-router'
import App from '@/App'
import home from '@/page/home'
import newgoods from '@/page/newgoods'
import shop from '@/page/shop'
import shopCategory from '@/page/shopCategory'
import shopGoods from '@/page/shopGoods'
import goodsDetail from '@/page/goodsDetail'
import market from '@/page/market'
import category from '@/page/category'
import usercenter from '@/page/usercenter'
import categoryGoods from '@/page/categoryGoods'
import mystore from '@/page/mystore'
import myupload from '@/page/myupload'
import settings from '@/page/settings'
import userInfo from '@/page/userInfo'
import modifyPassword from '@/page/modifyPassword'
import search from '@/page/search'
import login from '@/page/login'
import msgLogin from '@/page/msgLogin'
import bindTelephone from '@/page/bindTelephone'
import forgetPassword from '@/page/forgetPassword'
import regist from '@/page/regist'
import uploadTaobao from '@/page/uploadTaobao'
import websiteListPanel from '@/page/websiteListPanel'
import uploadSuccess from '@/page/uploadSuccess'
import goodsDetailForApp from '@/page/goodsDetailForApp'
import trademarkRegist from '@/page/trademarkRegist'
import awards from  '@/page/awards'
import about from '@/page/about'

Vue.use(Router)

export default new Router({
    //mode: 'history',
    routes: [
        {
            path: '/',
            redirect: '/home'
        },
        {
            path: '/home',
            component: home
        },
        {
            path: '/newgoods',
            component: newgoods
        },        
        {
            name: 'shop',
            path: '/shop/:id',
            component: shop
        },
        {
            path: '/shopCategory',
            component: shopCategory
        },
        {
            path: '/shopGoods',
            component: shopGoods
        },
        {
            name: 'goods',
            path: '/goodsDetail/:id',
            component: goodsDetail
        },
        {
            path: '/market',
            component: market
        },
        {
            path: '/category',
            component: category
        },
        {
            path: '/usercenter',
            component: usercenter
        },
        {
            path: '/cities',
            component: websiteListPanel
        },
        {
            path: '/categoryGoods',
            component: categoryGoods
        },
        {
            path: '/mystore',
            component: mystore
        },
        {
            path: '/myupload',
            component: myupload
        },
        {
            path: '/settings',
            component: settings
        },
        {
            path: '/userInfo',
            component: userInfo
        },
        {
            path: '/modifyPassword',
            component: modifyPassword
        },
        {
            path: '/search',
            component: search
        },
        {
            path: '/login',
            component: login
        },
        {
            path: '/msgLogin',
            component: msgLogin
        },
        {
            path: '/regist',
            component: regist
        },
        {
            path: '/forgetPassword',
            component: forgetPassword
        },
        {
            path: '/bindTelephone',
            component: bindTelephone
        },
        {
            name: 'upload',
            path: '/uploadTaobao/:id',
            component: uploadTaobao
        },
        {
            path: '/uploadSuccess',
            component: uploadSuccess
        },
        {
            path: '/goodsDetailForApp',
            component: goodsDetailForApp
        },
        {
            path: '/trademarkRegist',
            component: trademarkRegist
        },
        {
            path: '/awards',
            component: awards
        },
        {
            path: '/about',
            component: about
        }
    ]
})



