<template>
	<div>
		<common-head class="shop_headFixed" :view="{
            goback: true,
            pageTitle: '宝贝分类'
        }"></common-head>
        
        <div class="search-icon">
            <router-link :to="{path:'/search', query:{type:'shop', keywords:'', mode:'icon'}}"><i class="iconfont icon-search"></i></router-link>
        </div>
        <span slot="collect" class="collect">
            <i class="iconfont" :class="[{selected: stored}, {'icon-s-love': stored}, {'icon-love': !stored}]" @click="doCollectAndCancel"></i>
        </span>

		<div class="cat_list">
			<div class="cat_item" v-for="cat in cats">
				<h3 @click="gotoShopGoods(cat.scid)"><span class="catname">{{cat.catName}}</span><span class="num">{{cat.itemNum}}</span></h3>
				<div class="subcats">
					<ul>
						<li v-for="item in cat.subCats" @click="gotoShopGoods(item.scid)">
							{{item.catName}}<i class="iconfont icon-right"></i>
						</li>
					</ul>
				</div>
			</div>
		</div>

        <loading></loading>
	</div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import Loading from '../components/loading'
import {msgTip} from '../plugins/util'
import {getShopCat, hasCollected, addCollect, deleteCollect, ajax} from '../data/service'

export default{
	data(){
		return{
			shopId: this.$route.query.id,
			market: this.$route.query.market,
			shopNum: this.$route.query.shopNum,
			cats:[],
			stored: false,
			collectIds:[],
            collectId: ''
		}
	},
	created(){
		this.getShopCategory()
		this.shopIsCollected()
	},
    computed:{
        ...mapState({
            'userId': state => state.userId,
            'userType': state => state.userType,
            'website': state => state.website,
            'loading': state => state.loading,
        })
    },
	mixins:[ajax],
	methods: {
        ...mapActions(['initUserLoginState', 'startLoding']),

        loadingShow(){
            this.startLoding({status:true})
        },
        loadingHide(){
            this.startLoding({status:false})
        },
		//获取店铺内分类列表
		async getShopCategory(){
			this.loadingShow()

            this.ajax(await getShopCat(this.website, this.shopId), (resp) => {
				this.cats = resp.cats
			})

            this.loadingHide()
		},
        //是否已收藏该店铺
        async shopIsCollected(){
        	await this.initUserLoginState()

        	if(!this.userId){
        		return
        	}
            this.ajax(await hasCollected(this.website, 'shop', this.shopId), (resp) => {
                if(resp.collectId){
                    if(this.collectIds.indexOf(resp.collectId) < 0){
                        this.collectIds.push(resp.collectId)
                    }
                    this.collectId = resp.collectId
                    this.stored = true
                }
            })
        },
		//收藏|取消收藏店铺
        async doCollectAndCancel(){
            if(!this.userId){
            	this.$router.push('/login')
            }else if(this.userType == 2){
            	return msgTip('供应商不支持此操作', '我知道了')
            }else{
                if(this.stored){
                    this.ajax(await deleteCollect(this.website, 'shop', this.collectIds), (resp) => {
                        this.stored = false
                    })
                }else{
                    this.ajax(await addCollect(this.website, 'shop', this.shopId), (resp) => {
                        if(this.collectIds.indexOf(resp.collectId) < 0){
                            this.collectIds.push(resp.collectId)
                        }
                        this.collectId = resp.collectId
                        this.stored = true
                    })
                }
            }            
        },
        //跳转到商品列表页
        gotoShopGoods(id){
        	this.$router.push({path:'/shopGoods', query:{id:this.shopId, scid:id, market:this.market, shopNum:this.shopNum}})
        }
	},
	components: {
		commonHead,
        Loading,
	}
}
</script>

<style scoped>
.search-icon{position:fixed; right:.44rem; top:.12rem; z-index:300;}
.search-icon i{font-size:.22rem; color:#999;}
.collect{position:fixed; right:.12rem; top:.12rem; z-index:300;}
.collect i{font-size:.24rem; color:#999;}
.collect i.selected{color:#EC2E28;}

.cat_list{margin-top:.48rem;}
.cat_item{background:#fff; margin-bottom:.08rem; border:solid #d8d8d8; border-width:1px 0;}
.cat_item h3{font-size:.14rem; color:#333; padding:.1rem .16rem;}
.cat_item h3 span{display:inline-block; width:3rem; overflow:hidden; white-space:nowrap; text-overflow:ellipsis;}
.cat_item h3 .num{display:inline-block; float:right; width:.36rem; height:.2rem; line-height:.2rem; background:#cdced2; border-radius:.1rem; text-align:center; color:#fff; font-size:.12rem;}
.subcats{overflow:hidden;}
.subcats li{width:1.8rem; height:.36rem; line-height:.36rem; background:#f3f3f3; float:left; margin-left:.05rem; margin-bottom:.07rem; font-size:.12rem; position:relative; padding:0 .3rem 0 .1rem; overflow:hidden; white-space:nowrap; text-overflow:ellipsis;}
.subcats li i{position:absolute; font-size:.14rem; color:#CDCED2; right:.08rem;}
.subcats li a{display:block; color:#666;}
</style>


