<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: pageTitle
		}"></common-head>

		<goods-list :goods="goodsList" show-market="false" class="shop_goodslist"></goods-list>

		<div v-show="hasNext" @click="nextPage" class="next_page"><span>点击加载更多</span></div>

		<loading></loading>
	</div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import goodsList from '../components/goodsList'
import Loading from '../components/loading'
import {querySearchGoods, ajax} from '../data/service'
export default{
	data() {
		return{
			shopId: this.$route.query.id,
			scid: this.$route.query.scid,
			pageTitle: this.$route.query.market + this.$route.query.shopNum,
			goodsList: [],
			rerender: false,
			hasNext: false,
			pageIndex: 1,
		}
	},
	components: {
		commonHead,
		goodsList,
		Loading,
	},
	created(){
		this.getGoodsList()
	},
	mixins: [ajax],
	computed: {
		...mapState({
			'website': state => state.website,
			'loading': state => state.loading,
		})
	},
	methods: {
		...mapActions(['startLoding']),
        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },
		//获取商品列表
		async getGoodsList(){
			this.loadingShow()

			let options = {
				webSite: this.website,
				storeId: this.shopId,
				scid: this.scid,
				index: this.pageIndex,
				size: 30,
				type:3,
			}
			this.ajax(await querySearchGoods(options), (resp) => {
				if(this.rerender){
					this.rerender = false
					this.goodsList = resp.goodsList
				}else{
					this.goodsList = this.goodsList.concat(resp.goodsList)
				}
				this.hasNext = resp.hasNext
			})
			this.loadingHide()
		},
		nextPage(){
			this.pageIndex++
			this.getGoodsList()
		}
	}
}
</script>

<style scoped>
.shop_goodslist{padding-top:.48rem; background:#fff;}
</style>


