<template>
  	<div>
        <section class="user_info">
            <img v-if="!headUrl" src="../assets/headUrl.jpg">
            <img v-else :src="headUrl">
            <span class="user_name">{{userNick}}</span>
        </section>
        <section class="operate_list">
            <ul>
                <li><router-link to="/mystore"><i class="iconfont icon-c-store"></i><span>收藏夹</span><em class="iconfont icon-right"></em></router-link></li>
                <li><router-link to="/myupload"><i class="iconfont icon-c-upload"></i><span>已上传的商品</span><em class="iconfont icon-right"></em></router-link></li>     
            </ul>
            <ul>
                <li><router-link to="/awards"><i class="iconfont icon-c-award"></i><span>我的奖品</span><em class="iconfont icon-right"></em></router-link></li>
            </ul>
            <ul>
                <li><router-link to="/settings"><i class="iconfont icon-c-setting"></i><span>设置</span><em class="iconfont icon-right"></em></router-link></li>
            </ul>
        </section>

        <common-foot activeId="usercenter"></common-foot>
    </div>
</template>

<script>
import commonFoot from '../components/commonFoot'
import {getUserInfo, ajax} from '../data/service'

export default {
    data: function(){
    	return{
            headUrl:'',
            userNick:''
    	}
    },
    created(){
        this.getUserInfo()
    },
    mixins:[ajax],
    methods:{
        async getUserInfo(){
            var result = await getUserInfo()

            if(result.success){
                this.headUrl = result.headUrl
                this.userNick = result.userNick 
            }else{
                this.$router.push('/login') 
            }
        }
    },
    components:{
        commonFoot
    }
}
</script>

<style scoped>
.user_info{width:100%; height:2.1rem; background:url(../assets/userbg.png) no-repeat 0 0; background-size:100% 100%; text-align:center;}
.user_info img{width:.9rem; height:.9rem; border-radius:50% 50%; margin-top:.48rem; border:.05rem solid rgba(255,255,255,.2);}
.user_info span{display:block; font-size:.16rem; color:#fff; line-height:.3rem;}

.operate_list{margin-top:.3rem;}
.operate_list ul{margin-top:.15rem; background:#fff; padding:0 .14rem; border:1px solid #d8d8d8; border-width:1px 0;}
.operate_list li{border-bottom:1px solid #d8d8d8; padding:.1rem 0; font-size:.16rem; color:#333; overflow:hidden;}
.operate_list li a{display:block;}
.operate_list li i{float:left; font-size:.28rem; margin-right:.1rem;}
.operate_list li span{line-height:.32rem;}
.operate_list li em{float:right; color:#d8d8d8; font-size:.2rem; position:relative; top:.03rem; right:-.04rem;}
.operate_list ul li:last-child{border-bottom:0;}
.operate_list li i.icon-c-store{color:#7dc832;}
.operate_list li i.icon-c-upload{color:#4a90e2;}
.operate_list li i.icon-c-setting{color:#ff6c37;}
.operate_list li i.icon-c-award{color:#cb4ae2;}
</style>



