<template>
  	<div>
        <common-head :view="{
            searchBar: 2,
            togglePageType: true
        }"></common-head>
		<div class="category_list">
            <div class="category" v-for="category in catGroups">
                <h3><span>{{category.title}}</span></h3>
                <ul>
                    <li v-for="cat in category.cats">
                        <router-link :to="{path:'/categoryGoods', query:{pid:pid, cid:cat.cid,cname:cat.name}}">
                            <div class="img_box"><img :src="cat.imgsrc"></div>
                            <span>{{cat.name}}</span>
                        </router-link>                        
                    </li>
                </ul>
            </div> 
        </div>

        <common-foot activeId="category"></common-foot>

        <loading></loading>
    </div>
</template>

<script>
import {mapState, mapMutations, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import commonFoot from '../components/commonFoot'
import Loading  from  '../components/loading'
import {getGoodsCatList, ajax} from '../data/service'

export default {
    data: function(){
    	return{
            curPid:'',
            catGroups:'',
    	}
    },
    components: {
        commonHead,
        commonFoot,
        Loading,
    },
    created(){        
        this.getCatList()
    },
    mixins: [ajax],
    methods:{
        ...mapMutations(['setPageType']),
        ...mapActions(['startLoding']),

        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },
        async getCatList(){   
            this.loadingShow()

            this.ajax(await getGoodsCatList(this.website, this.pid), function(resp){
                this.catGroups = resp.catGroups
                this.curPid = this.pid
            })
            this.loadingHide()
        }
    
    },   
    computed: {
        ...mapState({
            'pid': state => state.pid,
            'website': state => state.website,
            'loading': state => state.loading
        })
    },   
    updated() {
        if(this.curPid != this.pid){
            this.getCatList();
        }
    }
}
</script>

<style scoped>
.common-head{padding-left:.12rem;}

.category_list{width:100%; margin-top:.48rem; padding-bottom:.6rem; padding-left:.1rem;}
.category h3{line-height:.36rem; text-align:center; position:relative;}
.category h3 span{display:inline-block; padding:0 .06rem; background:#f2f2f2; font-size:.14rem; color:#999; position:relative; z-index:10;}
.category h3:after{content:''; width:2.4rem; height:1px; background:#d8d8d8; position:absolute; top:50%; left:50%; margin-left:-1.2rem; z-index:2;}
.category ul{overflow:hidden;}
.category li{background:#fff; width:.88rem; height:1.06rem; float:left; border-left:1px solid #f2f2f2; border-bottom:1px solid #f2f2f2; text-align:center; }
.category li .img_box{width:.88rem; height:.8rem; padding-top:.08rem; overflow:hidden;}
.category li img{width:.7rem; height:.7rem;}
.category li span{font-size:.12rem; display:block; line-height:.24rem; width:100%; white-space:nowrap; text-overflow:ellipsis;}
</style>



