<template>
  	<div>
        <common-head :view="{
            goback: true,
            pageTitle: '今日新品',
            togglePageType: true,
        }"></common-head>
		
		<section class="goods_menu">
			<ul>
                <li v-for="item in catMenu" :class="{current: !keyword && curPid == item.pid || !item.pid && keyword == item.keyword}">
                    <span v-if="item.pid" @click="changePid(item.pid)">{{item.name}}</span>
                    <span v-else @click="changeKeyword(item.keyword)">{{item.name}}</span>
                </li>
			</ul>
		</section>
    
        <section class="goods-list"><goods-list :goods="goodsList"></goods-list></section>
        
        <div v-show="hasNext" @click="nextPage" class="next_page"><span>点击加载更多</span></div>
        
        <loading></loading>
    </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import goodsList from '../components/goodsImgList'
import Loading  from '../components/loading'
import {querySearchGoods, ajax} from '../data/service'

export default {
    name: 'newgoods',
    components:{
    	commonHead,
        goodsList,
        Loading,
    },
    data: function(){
    	return{
            categories: {
                30:  [
                    {pid:'30', name:'男装'},
                    {pid:'50011740', name:'男鞋'},
                    {keyword:'童装', name:'童装'},
                    {pid:'50006842', name:'箱包'},
                    {pid:'50010404', name:'配饰'}
                ],
                16: [
                    {pid:'16', name:'女装'},
                    {pid:'50006843', name:'女鞋'},
                    {keyword:'童装', name:'童装'},
                    {pid:'50006842', name:'箱包'},
                    {pid:'50010404', name:'配饰'}
                ],
            },
            catMenu: [],
            curPid: 30,
            keyword: '',
            goodsList: [],
            pageIndex: 1,
            hasNext: false,
            rerender: false,
            type: 1
    	}
    },
    created(){
        this.initCatMenu()
        this.queryGoodsList()
        
    },
    mixins: [ajax],
    methods:{
        ...mapActions(['startLoding']),
        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },
        initCatMenu(){
            this.catMenu = this.categories[this.curPid = this.pid]
        },
        async queryGoodsList(){
            this.loadingShow()

            let options = {
                keyword: this.keyword,
                webSite: this.website,   
                cid: this.curPid,
                index: this.pageIndex,
                size: 30,
                type: this.keyword ? 2 : 1,
            }
            this.ajax(await querySearchGoods(options), (resp) => {
                if(this.rerender){
                    this.rerender = false
                    this.goodsList = resp.goodsList
                }else{
                    this.goodsList = this.goodsList.concat(resp.goodsList)
                }
                this.hasNext = resp.hasNext
            })

            this.loadingHide()
        },
        nextPage(){
            this.pageIndex++
            this.queryGoodsList()
        },
        changePid(pid){
            if(pid != this.curPid){
                this.keyword = ''
                this.curPid = pid
                this.pageIndex = 1
                this.rerender = true
                this.queryGoodsList()
            }
        },
        changeKeyword(keyword){
            if(keyword != this.keyword){
                this.keyword = keyword
                this.curPid = this.pid
                this.pageIndex = 1
                this.rerender = true
                this.queryGoodsList()
            }
        }
    },
    computed: {
        ...mapState({
            'pid': state => state.pid,
            'website': state => state.website,
            'loading': state => state.loading,
        }),
    },
    watch: {
        pid(){
            this.changePid(this.pid)
            this.initCatMenu()
        },
    }
}
</script>

<style scoped>
.goods_menu{width:100%; height:.48rem; padding-top:.04rem; background:#fff; position:fixed; top:.46rem; left:0; box-shadow:0 1px .06rem rgba(0,0,0,.1);}
.goods_menu ul{display:flex; text-align:center;}
.goods_menu li{flex:1; font-size:.15rem;}
.goods_menu li span{display:inline-block; padding:0 .08rem; height:.44rem; line-height:.44rem; border-bottom:.02rem solid #fff;}
.goods_menu li.current span{border-color:#333;}

.goods-list{margin-top:.08rem; padding-top:.92rem; overflow:hidden;}
</style> 




