<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '关于四季星座网'
		}"></common-head>

		<div class="content">
			<p>杭州四季青，浙江著名的服装批发市场。在那里坐落着数栋批发市场，四季星座就是其中之一。</p>
			<p>四季星座更广为圈内人所知的，还是其自2008年建立的独立网站——四季星座网（www.571xz.com）。一件代发、0秒上新、淘宝订单同步、知识产权保护，四季星座网将方圆几里内的数栋批发市场内商户搬上了线，更有数以万计的卖家在这里完成批发订单。</p>
			<p>四季星座网（www.571xz.com）成立于2008年，是杭州石谷网络科技有限公司倾力打造的服饰资源整合的供应链平台，为经销商提供资源的集中服务的网络平台。</p>
			<p>于2011年起着力打造整合线下服装专业市场资源，先后成功运作置地国际电商基地、星座女装电子商务楼等线下专业网批实体市场，并与杭州四季青商圈、北京大红门商圈等其他传统实体市场保持长期而紧密的线上线下合作关系！</p>
			<p>四季星座网除四季星座入驻外，周边的电商基地、精品男装、四季星座、钱塘大厦、新杭派、置地国际、之江服饰、优品基地、意法服饰、二号基地、中星外贸、九天国际等商户都已上线，共计商家2300多户，覆盖的杭州本地电商卖家50万家，活跃的独立用户有5~6万人。</p>
			<p>目前四季星座网移动端APP同步上线，星座进货宝V2.2.0、星座供应商V2.2.0、星座微分享V1.1.0，移动端为档口和卖家带来更多便利。</p>
			<p>四季星座网的运营管理团队同时也是实体市场的管理者，四季星座网特色在于将网站和市场密切的联系起来，网站终将是为实体入驻的商铺而服务，起到档口和卖家之间的润滑剂。其人性化的特色服务引领档口和卖家健康有序的发展。构建一个全国性的集货源调拨、物流调拨于一体的批发市场生态圈。</p>

		</div>
	</div>
</template>

