
<template>
    <div>
        <common-head :view="{
            searchBar: 2,
       }"></common-head>
       
       <div class="market_list">
            <ul>
                <li v-for="(item, index) in markets" :class="{current: index == 0 && currentMarket =='' || currentMarket == item.id}" @click="getMarketListAndFloors(item.id)">
                   {{item.name}}
                </li>
            </ul>
        </div>

        <div class="floor_list">
            <div class="floor" v-for="floor in floors">
                <h2>{{floor.title}}</h2>
                <div class="stores">
                    <ul>
                        <li v-for="store in floor.stores">
                            <router-link :to="{name:'shop', params:{id:store.storeId}}">
                                {{store.num}}
                                <i v-if="store.isNew == 1"></i>
                            </router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
       
       <common-foot activeId="market"></common-foot>

       <loading></loading>
    </div>
</template>

<script>

import {mapState, mapActions} from 'vuex'
import commonHead from '@/components/commonHead'
import commonFoot from '@/components/commonFoot'
import Loading from '@/components/loading'
import {getMarketList, ajax} from '@/data/service'

export default {
    created(){        
        this.getMarketListAndFloors(this.$route.query.mid)
    },

    components: {
        commonHead,
        commonFoot,
        Loading,
    },

    data: function(){
    	return{
            markets: [],
            floors: [],
            currentMarket: 1
        }    
    },    
    computed:{
        ...mapState({
            'website': state => state.website,
            'loading': state => state.loading,
        })
    },
    mixins: [ajax],
    methods: {
        ...mapActions(['startLoding']),

        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },
        async getMarketListAndFloors(mid = ''){            
            this.loadingShow()

            this.ajax(await getMarketList(this.website, mid), function(res){
                var market = res.market;
                var markets = [{id:mid, name:market.marketName}];
                market.marketTags.forEach(function(item){
                    markets.push({id:item.mid, name:item.name});
                });
                markets.shift();                
                this.markets = markets;
                this.floors = market.floors;
                this.currentMarket = mid;
            });

            this.loadingHide()
        },
    
    },
}

</script>

<style scoped>
.common-head{padding-left:.12rem; padding-right:.12rem;}
.market_list{width:1.02rem; position:absolute; top:.44rem; bottom:.48rem; background:#fff; overflow-y:auto; -webkit-overflow-scrolling:touch;}
.market_list li{font-size:.14rem; text-align:center; line-height:.48rem; color:#333; border-left:.02rem solid #fff; border-bottom:1px solid #f2f2f2;}
.market_list li.current{border-left-color:#198adb; color:#198adb; background:#f2f2f2;}

.floor_list{width:2.52rem; position:absolute; top:.44rem; bottom:.48rem; left:1.12rem; overflow-y:auto; -webkit-overflow-scrolling:touch;}
.floor_list .floor{margin-bottom:.1rem;}
.floor_list .floor h2{line-height:.3rem; font-size:.14rem; color:#666; text-align:center;}
.floor_list .floor .stores{background:#fff; overflow:hidden;}
.floor_list .floor .stores li{border-left:1px solid #f2f2f2; border-bottom:1px solid #f2f2f2; width:33.33%; float:left; line-height:.48rem; font-size:.14rem; text-align:center; white-space:nowrap; overflow:hidden; text-overflow:ellipsis; position:relative;}
.floor_list .floor .stores li:last-child{border-right:1px solid #f2f2f2;}
.floor_list .floor .stores li i{position:absolute; width:.22rem; height:.21rem; right:-1px; top:0; background:url(../assets/new.png) no-repeat 0 0; background-size:100% 100%;}
</style>





