<template>
  	<div v-if="showStart" class="tb_temp">
        <common-head :view="{
            pageTitle: '开始时间'
        }"></common-head>  
        
        <go-back @backFormChild="backToParent('setStart')"></go-back>

        <section class="set_list">
            <ul>
                <li @click="tempToParent()"><span>立即</span><em class="iconfont icon-right"></em></li>
                <li @click="tempToParent('storage')"><span>放入仓库</span><em class="iconfont icon-right"></em></li>        
            </ul>
        </section>

    </div>
</template>

<script>
import commonHead from '../components/commonHead'
import goBack from '../page/uploadTbGoBack'

export default {
    data: function(){
    	return{
            
    	}
    },
    methods:{
        //向父组件传选中的值
        tempToParent(temp){
            this.$emit('valueFromStartTimeChild', temp)
        },
        backToParent(data){
            this.$emit('hideThisChild', data)
        }
    },
    components:{
        commonHead,
        goBack
    },
    props:['showStart']
}
</script>

<style scoped>
.tb_temp{position:absolute; background:#f2f2f2; z-index:110; width:100%; height:100%; left:0; top:0;}

.set_list{margin-top:.56rem; background:#fff}
.set_list ul{margin-top:.15rem; background:#fff; padding:0 .14rem;}
.set_list li{border-bottom:1px solid #d8d8d8; padding:.1rem 0; font-size:.16rem; color:#333; overflow:hidden;}
.set_list li a{display:block;}
.set_list li span{line-height:.32rem;}
.set_list li em{float:right; color:#d8d8d8; font-size:.2rem; position:relative; top:.03rem; right:-.04rem;}
.set_list ul li:last-child{border-bottom:0;}
</style>



