<template>
    <div>
        <common-head :view="{
            selectWebsite: true, 
            searchBar: 1,
            togglePageType: true
       }"></common-head>
    
        <div v-if="!pid" class="guide-pageType">
            <h3>您的主营是</h3>
            <ul>
                <li @click="setPageType('man')"><i class="iconfont icon-shirt"></i>男装</li>
                <li @click="setPageType('woman')"><i class="iconfont icon-dress"></i>女装</li>
            </ul>
        </div>
    
        <div class="banner">
            <div class="swiper-container" id="bannerSwiper">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="banner in banners">
                        <img :src="banner.imgsrc" @click="hrefToPage(banner.href)">
                        <!--<router-link :to="banner.href"><img :src="banner.imgsrc"></router-link>-->
                    </div>
                </div>
                <div class="swiper-pagination" id="bannerPagination"></div>
            </div>
        </div>
    
        <nav class="main_menu">
            <ul>
                <li><router-link to="newgoods"><img src="../assets/menu1.png"><span>今日新品</span></router-link></li>
                <li><router-link to="trademarkRegist"><img src="../assets/menu2.png"><span>商标注册</span></router-link></li>
                <li><router-link to="market"><img src="../assets/menu3.png"><span>逛市场</span></router-link></li>
                <li><router-link to="category"><img src="../assets/menu4.png"><span>商品库</span></router-link></li>
            </ul>
        </nav>
    
        <section class="recommend_shop">
            <h2><span>推荐档口</span></h2>
            <div class="swiper-container" id="shopSwiper">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <div class="shop_item" v-for="(item, index) in shops" v-if="index < 8">
                            <router-link :to="{name:'shop', params:{id:item.shopid}}">
                                <img :src="item.imgSrc">
                                <span>{{item.name}}</span>
                            </router-link>
                        </div>
                    </div>
                    <div class="swiper-slide" v-if="shops.length > 8 && shops.length < 16">
                        <div class="shop_item" v-for="(item, index) in shops" v-if="index >= 8">
                            <router-link  :to="{name:'shop', params:{id:item.shopid}}">
                                <img :src="item.imgSrc">
                                <span>{{item.name}}</span>
                            </router-link>
                        </div>
                    </div>
                </div>

            </div>
        </section>  
    
        <section class="goods_list">
            <h2><span>热销商品</span></h2>
            <goods-list :goods="goodsList"></goods-list>
        </section>
    
        <div class="placeholder"></div>
    
        <common-foot activeId="home"></common-foot>
    
        <loading></loading>
    </div>
    
</template>


<script>

import {mapState, mapMutations, mapActions} from 'vuex'
import commonHead from '@/components/commonHead'
import goodsList from '@/components/goodsImgList'
import commonFoot from '@/components/commonFoot'
import Loading  from '@/components/loading'
import {getIndexBanner, queryGoodsGoatList, ajax} from '@/data/service'

export default {
    created(){
        if(this.pid){
            this.getIndexBanner();
            this.getGoatShop();
            this.getGoodsList(); 
        }                       
    },
    
    components: {
        commonHead,
        goodsList,
        commonFoot,
        Loading,
    },
    data(){
        return {
            banners: [],
            shops: [],
            goodsList: []
        }
    },
    mixins: [ajax],
    methods: {    
        ...mapMutations(['setPageType']),
        ...mapActions(['startLoding']),

        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },

        //判断广告是哪种类型去不同的页面
        hrefToPage(href){
            if(href.indexOf('shop') > 0){
                this.$router.push({name:'shop', params:{id:href.split('=')[1]}})
            }else if(href.indexOf('item') > 0){
                this.$router.push({name:'goods', params:{id:href.split('=')[1]}})
            }else if(href.indexOf('market') > 0){
                this.$router.push({path:'/market', query:{mid:href.split('=')[1]}})
            }else{
               window.location.href = href
            }
        },

        //拼接广告位编号
        getSpreadCode(){
            let site = this.website.toUpperCase();            
            if(this.pid == 30){
                return site + '-APP-MAN'
            }else if(this.pid == 16){
                return site + '-APP-WOMAN'
            }
        },

        async getIndexBanner(){ //顶部banner轮播
            this.loadingShow()
            let spreadCode = this.getSpreadCode() + '-DT';
            this.ajax(await getIndexBanner(spreadCode, this.website), function(res){
                this.banners = res.spreads;                

                setTimeout(function(){
                    new Swiper('#bannerSwiper', {
                        pagination: '#bannerPagination',
                        autoplay: 3000,
                        loop: true
                    })
                })
            })
            this.loadingHide()
        },
        
        async getGoatShop(){ //获取推荐档口 
            this.loadingShow()
            let spreadCode = this.getSpreadCode() + '-TJDK';
            this.ajax(await queryGoodsGoatList(spreadCode, this.website), function(res){
                this.shops = res.spreads.map(function(item){
                    return {
                        shopid: item.storeId,
                        goodsid: item.goodsId,
                        imgSrc: item.imgsrc,
                        name: item.marketText + item.storeNum
                    }
                })
                setTimeout(function(){
                    new Swiper('#shopSwiper', {                
                        autoplay: 5000,
                        loop: true
                    });
                });
                
            })
            this.loadingHide()
        },
        
        async getGoodsList(){ //获取商品列表
            this.loadingShow()
            let spreadCode = this.getSpreadCode() + '-RM';
            this.ajax(await queryGoodsGoatList(spreadCode, this.website), function(resp){
                this.goodsList = resp.spreads.map(function(item){
                    return {
                        goodsid: item.goodsId,
                        imgSrc: item.imgsrc,
                        market: item.marketText + item.storeNum,
                        title: item.title,
                        price: item.piprice
                    }
                });                
            })
            this.loadingHide()
        },
        
    },
    computed: {
        ...mapState({
            'pid': state => state.pid, 
            'website': state => state.website,
            'loading': state => state.loading,
        })
    },
    
    watch: {
        pid(){ //类目改变，重新获取数据
            this.getIndexBanner();
            this.getGoatShop();
            this.getGoodsList();
        },
    }
    
}

</script>

<style scope>
.guide-pageType{width:100%; height:100%; position:fixed; top:0; left:0; text-align:center; z-index:1002; background:rgba(255,255,255,1);}
.guide-pageType h3{font-size:.2rem; margin-top:1.4rem; color:#198ADB;}
.guide-pageType ul{width:2.3rem; margin:0 auto;}
.guide-pageType li{margin-top:.44rem; width:2.3rem; height:.6rem; line-height:.6rem; font-size:.2rem; background:#198ADB; border-radius:.02rem; color:#fff;}
.guide-pageType li i{font-size:.28rem; margin-right:.2rem; display:inline-block; vertical-align:middle;}

.banner{width:100%; height:2rem; margin-top:.48rem;}
.banner .swiper-container{width:100%; height:2rem;}
.banner img{width:100%; height:100%;}

.main_menu{padding:.16rem 0; background:#fff;}
.main_menu ul{display:flex;}
.main_menu li{flex:1; text-align:center;}
.main_menu img{width:.5rem; height:.5rem; vertical-align:top;}
.main_menu span{display:block; font-size:.14rem; padding:.06rem 0;}

.recommend_shop{margin-top:.1rem; background:#fff;}
.recommend_shop h2,
.goods_list h2{position:relative; padding:0 .22rem; text-align:center; z-index:1; line-height:.36rem;}
.recommend_shop h2:after,
.goods_list h2:after{content:""; position:absolute; width:3.32rem; height:1px; background-color:#d8d8d8; top:50%; left:50%; margin-left:-1.66rem; }
.recommend_shop span{position:relative; z-index:2; display:inline-block; padding:0 .1rem; background:#fff; font-size:.16rem;}

.shop_item{width:.86rem; margin:.04rem .02rem; text-align:center; float:left;}
.shop_item:nth-child(4n+1){margin-left:.09rem;}
.shop_item img{width:.86rem; height:.86rem; border-radius:.04rem;}
.shop_item span{display:block; font-size:.1rem; line-height:.2rem; padding:0; overflow:hidden; white-space:nowrap; text-overflow:ellipsis;}

.goods_list{overflow:hidden;}
.goods_list h2 span{position:relative; z-index:2; display:inline-block; padding:0 .1rem; background:#f2f2f2; font-size:.18rem; }

.placeholder{width:100%; height:.48rem;}
</style>


