<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '设置'
		}"></common-head>
		<section class="setting_list">
			<ul>
				<li><router-link to="/userInfo"><label>个人资料</label><i class="iconfont icon-right"></i></router-link></li>
				<li><router-link to="/about"><label>关于四季星座网</label><i class="iconfont icon-right"></i></router-link></li>
			</ul>
		</section>

		<section class="login_out" @click="loginOut">退出登录</section>

	</div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import {loginOut, ajax} from '../data/service'
export default{
	data(){
		return{
		}
	},
	mixins:[ajax],
	computed:{
		...mapState(['userId', 'userType'])
	},
	methods:{
		//...mapActions(['initUserLoginState']),
		async loginOut(){
			this.ajax(await loginOut(), (resp) => {
				//this.initUserLoginState()
				this.userId = '',
				this.userType = '',
				this.$router.push('/home')
			})
		}
	},
	components:{
		commonHead
	}
}
</script>

<style scoped>
.setting_list{padding-top:.48rem;}
.setting_list li{margin-top:.1rem; background:#fff; font-size:.16rem; color:#666; padding:.12rem .16rem;}
.setting_list li i{float:right; color:#cdced2; margin-top:.04rem;}
.setting_list li a{display:block; color:#666;}

.login_out{width:100%; height:.5rem; line-height:.5rem; background:#198ADB; position:fixed; left:0; bottom:0; text-align:center; color:#fff; font-size:.16rem;}
</style>


