<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '已上传的商品'
		}"></common-head>		
		<section class="upload_menu">
			<ul>
				<li v-for="item in menus"><span @click="changeCurrentMenu(item.type)" :class="{current: currentMenu == item.type}">{{item.name}}</span></li>
			</ul>
		</section>

		<section class="upload_list">
			<div class="upload_item" v-for="item in goodsList" v-if="goodsList.length > 0">
				<div class="goods_img">
					<img :src="item.imgsrc">
					<span v-if="item.supperDown">档口已下架</span>
				</div>
				<div class="goods_info">
                    <h3>{{item.title}}</h3>
                    <span class="price"><i>¥</i>{{item.piprice}}</span>
                    <span class="nosale" v-if="currentMenu != 3" @click="instockGoods(item.uploadId)">下架商品</span>
                    <span class="nosaled" v-else>已下架</span>
                </div>
			</div>
			<div class="no_data" v-else>暂无数据</div>

		</section>
	
		<div v-show="hasNext" @click="nextPage" class="next_page"><span>点击加载更多</span></div>

		<loading></loading>
	</div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import Loading from '../components/loading'
import {getMyUploadGoods, instockMyItem, ajax} from '../data/service'
import {msgTip} from '../plugins/util'

export default{
	data(){
		return{
			menus: [
				{type:1, name:'全部商品'},
				{type:2, name:'待处理'},
				{type:3, name:'淘宝已下架'}
			],
			currentMenu:1,
			index: 1,
			size: 30,
			goodsList: [],
			rerender: false,
			hasNext: false
		}
	},
	created(){
		this.getMyUpload()
	},
	components:{
		commonHead,
		Loading,
	},
	mixins:[ajax],
	computed: {
		...mapState({
			'website': state => state.website,
			'loading': state => state.loading
		})
	},
	methods:{
		...mapActions(['startLoding']),
		loadingShow(){
			this.startLoding({status:true})
		},
		loadingHide(){
			this.startLoding({status:false})
		},
		//获取列表
		async getMyUpload(){
			this.loadingShow()

			this.ajax(await getMyUploadGoods(this.website, this.currentMenu, this.index, this.size), function(resp){
				if(this.rerender){
                    this.rerender = false
                    this.goodsList = resp.items
                }else{
                    this.goodsList = this.goodsList.concat(resp.items)
                }				
				this.hasNext =  resp.hasNext
			})

			this.loadingHide()
		},
		//下架商品
		async instockGoods(uploadId){
			this.ajax(await instockMyItem(this.website, uploadId),function(resp){
				this.index = 1
				this.getMyUpload()
			})
		},
		changeCurrentMenu(type){
			this.currentMenu = type
			this.index = 1
			this.rerender = true
			this.getMyUpload()
		},
		nextPage(){
            this.index++
            this.getMyUpload()
        },
	}
}
</script>

<style scoped>
.upload_menu{width:100%; height:.48rem; padding-top:.04rem; background:#fff; position:fixed; top:.48rem; left:0; box-shadow:0 1px .06rem rgba(0,0,0,.1); z-index:10;}
.upload_menu ul{display:flex; text-align:center;}
.upload_menu li{flex:1; font-size:.15rem;}
.upload_menu li span{display:inline-block; padding:0 .08rem; height:.44rem; line-height:.44rem; border-bottom:.02rem solid #fff;}
.upload_menu li span.current{border-color:#333;}

.upload_list{background:#fff; margin-top:.96rem;}
.upload_item{padding:0 .12rem; padding-top:.16rem; display:flex;}
.upload_item:last-child{padding-bottom:.16rem;}
.upload_item .goods_img{width:.96rem; height:.96rem; position:relative; margin-right:.12rem; z-index:0;}
.upload_item .goods_img img{width:.96rem; height:.96rem;}
.upload_item .goods_img span{position:absolute; width:100%; height:.2rem; line-height:.22rem; text-align:center; background:rgba(0,0,0,.2); color:#fff; font-size:.12rem; left:0; bottom:0;}
.upload_item .goods_info{flex:1; border-bottom:1px solid #d8d8d8;}
.upload_item .goods_info h3{font-size:.12rem; color:#333; line-height:.2rem; padding-right:.06rem; overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:2;-webkit-box-orient:vertical; margin-bottom:.24rem;}
.upload_item .goods_info .price{float:left; color:#f40; font-size:.16rem;}
.upload_item .goods_info .price i{font-size:.12rem; margin-right:.02rem;}
.upload_item .goods_info .nosale,
.upload_item .goods_info .nosaled{float:right; width:.76rem; height:.24rem; line-height:.24rem; text-align:center; font-size:.12rem; color:#f40; border:1px solid #d40; border-radius:.12rem;}
.upload_item .goods_info .nosaled{border:none; color:#999; text-align:right;}
</style>



