<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '上传到淘宝'
		}"></common-head>
		<div class="content">
			<div class="goods_info">
				<img :src="imgsrc">
				<div class="goods_base">
					<h3>{{title}}</h3>
					<div class="goods_box">
						<span class="price"><em>￥</em>{{piPrice}}</span>
						<span class="goods_num">{{goodsNo}}</span>
					</div>
				</div>
			</div>
			
			<div class="tb_title">
				<h3>商品标题</h3>
				<div class="tb_titbox">
					<textarea v-model="titleDesc" @input="getTitleNum" class="text">{{titleDesc}}</textarea>
					<div class="words_num"><span class="num_exceed" v-if="numExceed">不能超过30个字或60个字符!</span><span class="num_record"><em :class="{red: numExceed == true}">{{titleNum}}</em>/60</span></div>
				</div>
			</div>
			
			<div class="tb_setting profit">
				<ul>
					<li>
						<label>利润：￥{{profit}}</label>
						<span>￥{{profit}}</span>
					</li>
				</ul>
			</div>

			<div class="tb_setting">
				<ul>
					<li @click="showTemp">
						<label>运费模板</label>
						<span v-if="tempVal">{{tempVal}}</span>
						<span v-else>请选</span>
						<i class="iconfont icon-right"></i>
					</li>
					<li @click="showCatList">
						<label>商品分类</label>
						<span v-if="catIds.length > 0">{{catVals}}</span>
						<span v-else>请选择（可不选）</span>
						<i class="iconfont icon-right"></i>
					</li>
				</ul>
			</div>
			
			<div class="tb_setting saled">
				<ul>
					<li  @click="showRefund">
						<label>退换货承诺</label>
						<span>{{refundVal}}</span>
						<i class="iconfont icon-right"></i>
					</li>
					<li @click="showStartTime">
						<label>开始时间</label>
						<span>{{startVal}}</span>
						<i class="iconfont icon-right"></i>
					</li>
					<li @click="showNeedCase">
						<label>橱窗推荐</label>
						<span>{{caseVal}}</span>
						<i class="iconfont icon-right"></i>
					</li>
				</ul>
			</div>
		</div>		
		<!--<div class="upload_btn" @click="uploadBtn">上传到淘宝</div>-->
		<div class="upload_btn">上传到淘宝</div>

		<post-temp :show-temp="showPostTemp" @valueFromTempChild="setTempVal" @hideThisChild="hidePage"></post-temp>
		<set-refund :show-refund="showSetRefund" @valueFromRefundChild="setRefundVal" @hideThisChild="hidePage"></set-refund>
		<set-start :show-start="showSetStart" @valueFromStartTimeChild="setStartTime" @hideThisChild="hidePage"></set-start>
		<need-case :show-case="showSetCase" @valueFromCaseChild="setCaseVal" @hideThisChild="hidePage"></need-case> 
		<set-cat :show-cat="showSetCat" @valueFromCatChild="setCatVals" @hideThisChild="hidePage"></set-cat>

		
		<div v-show="showShadow" class="up_shadow" @touchmove.prevent></div>
		<div v-show="showShadow" class="up_layer">
			<div class="up_stepList">
				<div class="up_step">
					<div class="up_info" :class="{failed: step5State=='失败'}">
						<span class="load_img" :class="{loading: step5State == '加载'}"></span>步骤5/5 - {{step5State}}
					</div>
					<p>生成手机端</p>
				</div>
				<div class="up_step">
					<div class="up_info" :class="{failed: step4State=='失败'}">
						<span class="load_img" :class="{loading: step4State == '加载'}"></span>步骤4/5 - {{step4State}}
					</div>
					<p>上传到淘宝</p>
				</div>
				<div class="up_step">
					<div class="up_info" :class="{failed: step3State=='失败'}">
						<span class="load_img" :class="{loading: step3State == '加载'}"></span>步骤3/5 - {{step3State}}
					</div>
					<p>生成主图</p>
				</div>
				<div class="up_step">
					<div class="up_info" :class="{failed: step2State=='失败'}">
						<span class="load_img" :class="{loading: step2State == '加载'}"></span>步骤2/5 - {{step2State}}
					</div>
					<p>生成属性图</p>
				</div>
				<div class="up_step">
					<div class="up_info" :class="{failed: step1State=='失败'}">
						<span class="load_img" :class="{loading: step1State == '加载'}"></span>步骤1/5 - {{step1State}}
					</div>
					<p>生成详情图</p>
				</div>
			</div>
			<div class="cancel_upload" @click="cancelUpload">取消</div>
		</div>

	</div>
</template>

<script>
import {mapState} from 'vuex'
import commonHead from '../components/commonHead'
import postTemp from '../page/tbPostTemp'
import setCat from '../page/tbSetCategory'
import setRefund from '../page/tbSetRefund'
import setStart from '../page/tbSetStartTime'
import needCase from '../page/tbSetCase'
import {msgTip} from '../plugins/util'
import {getTbGoodsInfo, createTbPics, uploadToTb, creatMobile, ajax} from '../data/service'
export default{
	data(){
		return{
			titleNum:0,
			titleDesc:'',
			numExceed:false,
			goodsId: this.$route.params.id,
			title: '',
			imgsrc: '',
			piPrice: '',
			goodsNo: '',
			profit: '',
			showPostTemp: false, //运费模板
			tempId: '', //请求参数
			tempVal:'', 
			showSetCat: '', //设置分类
			catIds: '', //请示参数
			catVals: '',
			showSetRefund: false, //设置退换货
			refundBoolean: true, //请求参数
			refundVal: '是',
			showSetStart: false, //设置开始时间			
			startTime: '', //请求参数
			startVal:'立即',
			showSetCase: false, //设置橱窗推荐
			caseBoolean: false, //请求参数
			caseVal: '不加入',
			showShadow: false, //显示上传淘宝步骤框
			step1State: '', //步骤1状态
			step2State: '', //步骤2状态
			step3State: '', //步骤3状态
			step4State: '', //步骤4状态
			step5State: '', //步骤5状态
			numIid:'', //上传成功后的淘宝numIid
		}
	},
	components:{
		commonHead,
		postTemp,
		setCat,
		setRefund,
		setStart,
		needCase
	},
	created(){
		let that = this
		this.getGoodsInfo(function(){
			that.titleNum = that.getByteLen(that.titleDesc)
		})
		
	},
	mixins:[ajax],
	computed: {
		...mapState(['website'])
	},
	methods:{
		//获取商品信息
		async getGoodsInfo(callback){
			this.ajax(await getTbGoodsInfo(this.website, this.goodsId), function(resp){
				this.titleDesc = this.title = resp.title,
				this.imgsrc = resp. imgsrc,
				this.piPrice = resp.piPrice,
				this.goodsNo = resp.goodsNo,
				this.profit = resp.profit
				callback()
			})
		},
		//上传淘宝图片
		async createTbImg(type){			
			this.ajax(await createTbPics(this.website, this.goodsId, type), function(resp){
				if(type == 1){
					this.step1State = '加载'
				}else if(type == 2){
					this.step2State = '加载'
				}else if(type == 3){
					this.step3State = '加载'
				}
				if(resp.success == true){
					if(type == 1){
						this.step1State = '完成'
					}else if(type == 2){
						this.step2State = '完成'
					}else if(type == 3){
						this.step3State = '完成'
					}
				}else{
					if(type == 1){
						this.step1State = '失败'
					}else if(type == 2){
						this.step2State = '失败'
					}else if(type == 3){
						this.step3State = '失败'
					}
				}
			})
		},
		//上传到淘宝
		async uploadTaobao(){			
			let options = {
				goodsId: this.goodsId,
				templateId: this.tempId,
				tbCatIds: this.catIds,
				title: this.title,
				price: this.piPrice,
				canRefund: this.refundBoolean,
				listTime: this.startTime,
				needCase: this.caseBoolean
			}
			this.ajax(await uploadToTb(options), function(resp){
				this.step4State = '加载'
				if(resp.success == true){
					this.step4State = '完成'
					this.numIid = resp.numIid					
				}else{
					this.step4State = '失败'
				}
			})	
		},
		//生成手机端
		async creatMobileDetail(){			
			if(this.numIid){				
				this.ajax(await creatMobile(this.numIid), function(resp){	
					this.step5State = '加载'				
					if(resp.success == true){
						this.step5State = '完成'
					}else{
						this.step5State = '失败'
					}
				})
			}else{
				this.step5State = '失败'
			}
		},
		//上传到淘宝按钮
		async uploadBtn(){
			if(this.numExceed == false){
				this.showShadow = true
				await this.createTbImg(1)
				await this.createTbImg(2)
				await this.createTbImg(3)
				await this.uploadTaobao()
				await this.creatMobileDetail()
				if(this.step1State == '完成' && this.step2State == '完成' && this.step3State == '完成' && this.step4State == '完成' && this.step5State == '完成'){
					this.$router.push({path:'/uploadSuccess', query:{id:this.goodsId}})
				}
			}

		},
		//关闭上传步骤显示框
		cancelUpload(){
			this.showShadow = false
		},
		//显示运费模板
		showTemp(){
			this.showPostTemp = true
		},
		//获取从运费模板子组件传过来的值
		setTempVal(data){
			this.showPostTemp = false
			this.tempId = data.id
			this.tempVal = data.item
		},
		//显示淘宝分类
		showCatList(){
			this.showSetCat = true
		},
		//获取从分类列表组件传过来的值
		setCatVals(data){
			this.showSetCat = false
			this.catIds = data.ids
			this.catVals = (data.items).join(',')
		},
		//显示退换货
		showRefund(){
			this.showSetRefund = true
		},
		//获取从退换货组件传来的值
		setRefundVal(data){
			this.showSetRefund = false
			this.refundBoolean = data
			this.refundVal = this.refundBoolean ? '是' : '否'
		},
		//显示开始时间
		showStartTime(){
			this.showSetStart =  true
		},
		//获取从开始时间组件传来的值
		setStartTime(data){
			this.showSetStart = false
			if(data){
				this.startTime = data
				this.startVal = data ? '放入仓库' : '立即'
			}
		},
		//显示橱窗推荐
		showNeedCase(){
			this.showSetCase = true
		},
		//获取从橱窗推荐组件传来的值
		setCaseVal(data){
			this.showSetCase =  false
			this.caseBoolean = data
			this.caseVal = this.caseBoolean ? '加入' : '不加入'
		},

		//子组件返回按钮
		hidePage(data){
			if(data == 'postTemp'){
				this.showPostTemp = false
			}else if(data == 'setRefund'){
				this.showSetRefund = false
			}else if(data == 'setStart'){
				this.showSetStart =  false
			}else if(data == 'setCase'){
				this.showSetCase =  false
			}else if(data == 'setCat'){
				this.showSetCat = false
			}			
		},
		//判断中英文字数
		getByteLen(val) {
            let len = 0;
            for(var i = 0; i < val.length; i++) {
                var a = val.charAt(i);
                if(a.match(/[^\x00-\xff]/ig) != null){
                    len += 2;
                }else{
                    len += 1;
                }
            }
            return len;
        },
		//获取标题字数
		getTitleNum(){
 			this.titleNum = this.getByteLen(this.titleDesc)
 			if(this.titleNum > 60){
 				this.numExceed = true
 			}else{
 				this.numExceed = false
 			}
		}
	}
}
</script>

<style scoped>
.content{position:absolute; width:100%; height:calc(100% - .98rem); left:0; top:.48rem; z-index:10; overflow-y:auto; -webkit-overflow-scrolling:touch;}
.goods_info{background:#fff; padding:.12rem; display:flex;}
.goods_info img{width:.96rem; height:.96rem;}
.goods_info .goods_base{flex:1; margin-left:.15rem;}
.goods_info .goods_base h3{font-size:.12rem; color:#333; line-height:.22rem; overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:2; -webkit-box-orient:vertical;}
.goods_info .goods_box{overflow:hidden; margin-top:.24rem; display:flex;}
.goods_info .goods_box .price{color:#EC2E28; font-size:.16rem; flex:1;}
.goods_info .goods_box .price em{font-size:.12rem; margin-right:.02rem; font-style:normal;}
.goods_info .goods_box .goods_num{flex:1; color:#999; font-size:.12rem; text-align:right; padding-top:.05rem;}

.tb_title{margin-top:.08rem;}
.tb_title h3{color:#999; font-size:.14rem; padding:.1rem .12rem;}
.tb_titbox{background:#fff; position:relative; z-index:1; padding:.16rem .12rem .1rem;}
.tb_titbox .text{width:100%; color:#333; font-size:.14rem; height:.4rem; line-height:.2rem; text-align:left;}
.tb_titbox .words_num{font-size:.12rem; color:#666; height:.2rem; overflow:hidden;}
.tb_titbox .words_num .num_exceed{float:left; color:#f00;}
.tb_titbox .words_num .num_record{float:right; text-align:right;}
.tb_titbox .words_num em{font-style:normal;}
.tb_titbox .words_num .red{color:#f00;}

.tb_setting{margin-top:.09rem; padding:0 .12rem; background:#fff;}
.tb_setting li{border-bottom:1px solid #d8d8d8; display:flex; padding:.15rem 0; line-height:.2rem; overflow:hidden;}
.tb_setting li:last-child{border-bottom:0;}
.tb_setting li label{font-size:.16rem; color:#666; width:1.2rem; text-align:left;}
.tb_setting li span{font-size:.14rem; color:#d8d8d8; flex:1; text-align:right; overflow:hidden; white-space:nowrap; text-overflow:ellipsis;}
.tb_setting li .icon-right{color:#d2d2d2; font-size:.14rem; width:.28rem; text-align:right;}
.profit li span{font-size:.16rem;}
.saled li span{color:#666;}

.upload_btn{position:fixed; width:100%; height:.5rem; line-height:.5rem; background:#198ADB; left:0; bottom:0; text-align:center; font-size:.16rem; color:#fff; z-index:100;}

.up_shadow{position:fixed; width:100%; height:100%; top:0; left:0; background:rgba(0,0,0,.2); z-index:200;}
.up_layer{position:fixed; width:2.7rem; height:3.35rem; background:#fff; border-radius:.04rem; z-index:210; top:50%; left:50%; margin-top:-1.67rem; margin-left:-1.35rem;}
.up_stepList{padding-left:.5rem; padding-top:.1rem;}
.up_step{font-size:.14rem; color:#333; margin:.1rem 0;}
.up_step .up_info{overflow:hidden;}
.up_step .failed{color:#f00;}
.up_step .load_img{display:inline-block; width:.18rem; vertical-align:middle; height:.18rem; margin-right:.06rem; background:url(../assets/upTb.png) 
	no-repeat 0 0 / 100% 100%;}
.up_step .loading{background-image:url(../assets/upTbLoad.gif);}
.up_step p{padding:.04rem 0 0 .24rem; font-size:.12rem; color:#999;}
.cancel_upload{position:absolute; width:100%; height:.5rem; line-height:.5rem; background:#198ADB; color:#fff; text-align:center; font-size:.14rem; border:0 0 .04rem .04rem;bottom:0; left:0;}
</style>


