<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '上传到淘宝'
		}"></common-head>

		<div class="content">
			<div class="goods_info">
				<img :src="imgsrc">
				<div class="goods_base">
					<h3>{{title}}</h3>
					<div class="goods_box">
						<span class="price"><em>￥</em>{{price}}</span>
						<span class="pi_price">批价：￥{{piPrice}}</span>
					</div>
				</div>
			</div>

			<div class="upload_success">
				<i class="iconfont icon-s-checked"></i>
				<h3><span>商品上传成功</span></h3>
				<p>离小目标又近了一步</p>
			</div>
			
			<div class="other_btns">
				<router-link :to="{name:'goods', params:{id: goodsId}}">返回商品详情页</router-link>
				<router-link to="/myupload">查看已上的商品</router-link>
			</div>

		</div>

	</div>
</template>

<script>
import {mapState} from 'vuex'
import commonHead from '../components/commonHead'
import {getTbGoodsInfo, ajax} from '../data/service'

export default{
	data(){
		return{
			goodsId: this.$route.query.id,
			imgsrc: '',
			title: '',
			price: '',
			piPrice: '',
		}
	},
	components:{
		commonHead
	},
	created(){
		this.getGoodsInfo()
	},
	computed:{
		...mapState(['website'])
	},
	mixins:[ajax],
	methods:{
		//获取商品信息
		async getGoodsInfo(){
			this.ajax(await getTbGoodsInfo(this.website, this.goodsId), function(resp){
				this.title = resp.title,
				this.imgsrc = resp. imgsrc,
				this.price = resp.price,
				this.piPrice = resp.piPrice
			})
		},
	}
	
}
</script>

<style scoped>
.content{position:absolute; width:100%; height:calc(100% - 1.06rem); left:0; top:.56rem; z-index:10; overflow-y:auto; -webkit-overflow-scrolling:touch;}
.goods_info{background:#fff; padding:.12rem; display:flex;}
.goods_info img{width:.96rem; height:.96rem;}
.goods_info .goods_base{flex:1; margin-left:.15rem;}
.goods_info .goods_base h3{font-size:.12rem; color:#333; line-height:.22rem; overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:2; -webkit-box-orient:vertical;}
.goods_info .goods_box{overflow:hidden; margin-top:.24rem; display:flex;}
.goods_info .goods_box .price{color:#EC2E28; font-size:.16rem;}
.goods_info .goods_box .price em{font-size:.12rem; margin-right:.02rem; font-style:normal;}
.goods_info .goods_box .pi_price{color:#999; font-size:.12rem; margin-left:.2rem; padding-top:.03rem;}

.upload_success{text-align:center; padding-top:.06rem;}
.upload_success i{color:#00CD32; font-size:1rem;}
.upload_success h3{margin-top:.2rem; height:.24rem; font-size:.18rem; color:#333; position:relative;}
.upload_success h3:before{content:''; width:3.2rem; border-bottom:1px solid #d8d8d8; left:50%; top:50%; margin-left:-1.6rem; position:absolute; text-align:center;}
.upload_success h3 span{display:inline-block; padding:0 .1rem; background:#f2f2f2; position:relative;}
.upload_success p{font-size:.12rem; color:#999; margin-top:.04rem;}

.other_btns{padding:.2rem 0;}
.other_btns a{display:block; width:2.8rem; height:.5rem; line-height:.5rem; background:#fff; color:#999; font-size:.14rem; margin:.12rem auto 0 auto; border:1px solid #d8d8d8; border-radius:.04rem; text-align:center;}
</style>


