<template>
	<section>
        <div class="list_item" v-for="(item, index) in goods">
            <router-link :to="{name:'goods', params:{id:item.goodsid}}">
                <img :src="item.imgSrc">
                <h3>{{item.title}}</h3>
                <div class="info">
                    <span class="price"><i>¥</i>{{item.price}}</span>
                    <span class="market" v-if="!showMarket">{{item.market}}</span>
                </div>
            </router-link>
        </div>
	</section>
</template>

<script>
export default{
	data(){
        return{            
        }
    },
    props:['showMarket', 'goods']
}
</script>


<style scoped>
.list_item{width:1.84rem; background:#fff; float:left; margin-bottom:.08rem; padding-bottom:.08rem;}
.list_item:nth-child(2n){float:right;}
.list_item img{width:1.84rem; height:1.84rem;}
.list_item h3{font-size:.14rem; padding:0 .08rem; margin:.06rem 0; line-height:.16rem; height:.3rem; overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:2;-webkit-box-orient:vertical;}
.list_item .info{padding:.04rem .08rem; font-size:.12rem; line-height:.16rem;}
.list_item .info .price{float:left; color:#f40; font-size:.16rem;}
.list_item .info .price i{font-size:.12rem; margin-right:.02rem;}
.list_item .info .market{float:right;color:#999;}

</style>


