<template>
    <header class="common-foot" :class="activeId + 'Actived'">
        <ul>
			<li v-for="item in menus" :class="item.className">
				<router-link :to="item.href"><i></i><span>{{item.menuName}}</span></router-link>
			</li>
			<li @click="toDifferentPage" class="usercenter"><i></i><span>我的</span></li>
		</ul>
    </header>
</template>

<script>
import {mapState, mapActions} from 'vuex'
export default {
    data(){
		return{
			menus:[
				{menuName:'首页', className:'home', href:'/home'},
				{menuName:'逛市场', className:'market', href:'/market'},
				{menuName:'分类', className:'category', href:'/category'}
			],		
		}
	},
    created(){
        this.initUserLoginState()
    },
    computed:{
        ...mapState(['userId'])
    },
    props: ['activeId'],
    methods:{
        ...mapActions(['initUserLoginState']),
        //是否登录去不同的页面
    	toDifferentPage(){
    		if(this.userId){
                this.$router.push('/usercenter')
            }else{
                this.$router.push('/login')
            }
    	}
    }
}
</script>


<style scoped>
.common-foot{position:fixed; width:100%; height:.48rem; background:#fff; box-shadow:0 -1px 5px rgba(0,0,0,.1); left:0; bottom:0; z-index:20;}
.common-foot ul{display:flex; padding:.06rem 0;}
.common-foot li{flex:1; text-align:center;}
.common-foot li i{display:inline-block; width:.24rem; height:.24rem; background-repeat:no-repeat; background-position:0 0; background-size:100% 100%;}
.common-foot li span{display:block; font-size:.1rem; margin-top:-.04rem;}
.common-foot li.home i{background-image:url(../assets/home.png);}
.common-foot li.market i{background-image:url(../assets/market.png);}
.common-foot li.category i{background-image:url(../assets/category.png);}
.common-foot li.usercenter i{background-image:url(../assets/usercenter.png);}

.common-foot.homeActived li.home span,
.common-foot.marketActived li.market span,
.common-foot.categoryActived li.category span,
.common-foot.usercenterActived li.usercenter span{color:#198ADB;}

.common-foot.homeActived li.home i{background-image:url(../assets/homeAct.png);}
.common-foot.marketActived li.market i{background-image:url(../assets/marketAct.png);}
.common-foot.categoryActived li.category i{background-image:url(../assets/categoryAct.png);}
.common-foot.usercenterActived li.usercenter i{background-image:url(../assets/usercenterAct.png);}
</style>


