<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: pageName,
			searchIcon: true
		}"></common-head>
		<div class="type_menu">
			<ul>
				<li v-for="cat in menus" @click="changOrderBy(cat.type)" :class="{current: orderType == cat.type}">
					<span>{{cat.name}}</span>
				</li>
				<li @click="changOrderBy('price-desc')" :class="{current: priceType == 'price-desc'}" v-if="this.priceType == 'price-desc'"><span>价格</span></li>
				<li @click="changOrderBy('price-asc')" :class="{current: priceType == 'price-asc'}" v-else><span>价格</span></li>
				
			</ul>
			<i :class="['iconfont', 'icon_change', {'icon-imglist':!imgListMode}, {'icon-list': imgListMode}]" @click="changeListMode()"></i>
			<span class="filter" @click="filterDet()">筛选 <i class="iconfont icon-filter"></i></span>
		</div>
			
		<div v-if="goodsList.length > 0">
			<div class="goods_imglist" v-if="imgListMode">
				<goods-img-list :goods="goodsList"></goods-img-list>
			</div>
			<div class="goods_list" v-else>
	            <goods-list :goods="goodsList"></goods-list>
			</div>
		</div>
		<div class="no_data" v-else>暂无数据</div>	

		<div v-show="hasNext" @click="nextPage" class="next_page"><span>点击加载更多</span></div>	

		<div class="shade_layer" v-if="filterShow" @touchmove.prevent></div>
		<transition name="slide-aside">
		    <div v-if="filterShow" class="filter_layer">		    	
		    	<div class="title">筛选</div>
				<div class="content">
					<!--总类-->
					<div class="filter_item">
						<div class="item_tit">总类 <em>|</em> {{curPname}} </div>
						<ul class="items">
							<li v-for="item in allCatMenu" :class="{current: curPname == item.name}" @click="changePid(item.pid, item.name)"><i class="iconfont icon-correct"></i>{{item.name}}</li>
						</ul>
					</div>
					<!--分类-->
					<div class="filter_item">
						<div class="item_tit">分类 <em>|</em> {{currentCat}}
							<span v-if="subCatMenu.length > 5" @click="openCat">全部<i :class="['iconfont', 'icon-down', {'icon-up': catIsOpen}]"></i></span>
						</div>
						<ul class="items">
							<li :class="{current: currentCat == '全部'}" @click="changeSubCat('', '全部')"><i class="iconfont icon-correct"></i>全部</li>
							<li v-for="item in showSubMenu" :class="{current: currentCat == item.name}" @click="changeSubCat(item.cid, item.name)"><i class="iconfont icon-correct"></i>{{item.name}}</li>
						</ul>
					</div>
					<!--市场-->
					<div class="filter_item">
						<div class="item_tit">市场 <em>|</em> {{currentMarket}}
							<span v-if="markets.length > 5" @click="openMarket">全部<i :class="['iconfont', 'icon-down', {'icon-up': marketIsOpen}]"></i></span>
						</div>
						<ul class="items">
							<li :class="{current: currentMarket == '全部'}" @click="changeMarket('', '全部')"><i class="iconfont icon-correct"></i>全部</li>
							<li v-for="item in showMarket" :class="{current: currentMarket == item.name}" @click="changeMarket(item.mid, item.name)"><i class="iconfont icon-correct"></i>{{item.name}}</li>
						</ul>
					</div>
					<!--价格区间-->
					<div class="filter_item price_item">
						<div class="item_tit">价格区间</div>
						<div class="price_range">
							<input type="text" v-model="startPrice" name="lowestPrice" placeholder="最低价" pattern="[0-9]*">
							<em></em>
							<input type="text" v-model="endPrice" name="highestPrice" placeholder="最高价" pattern="[0-9]*">
						</div>
					</div>
					<!--发布时间-->
					<div class="filter_item">
						<div class="item_tit">发布时间</div>
						<ul class="items">
							<li :class="{current: publishD == ''}"><i class="iconfont icon-correct"></i>全部</li>
							<li v-for="item in publishTime" :class="{current: publishD == item}" @click="getCurrentTime(item)"><i class="iconfont icon-correct"></i>{{item}}日内</li>
						</ul>
					</div>
				</div>
				<div class="filter_btns">
					<span class="filter_cancel" @click="filteClose()">取消</span>
					<span class="filter_sure" @click="filterSure()">确定</span>
				</div>

   			</div>
		</transition>	
		
		<loading></loading>

	</div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import goodsImgList from '../components/goodsImgList'
import goodsList from '../components/goodsList'
import Loading from '../components/loading'
import {querySearchGoods, getAllCats, getSubCats, getMarketByPid, ajax} from '../data/service'

export default{
	data(){
		return{
			menus: [
				{type:'common', name:'综合'},
				{type:'xp', name:'新品'},
				{type:'popular', name:'人气'}
			],
			priceType:'',
			pageName:this.$route.query.cname,
			orderType: 'common',
			marketId: '',
			keyword:'',
			curPid: this.$route.query.pid,
			cid: this.$route.query.cid,
			startPrice: '',
			endPrice: '',			
			goodsList: [],
            pageIndex: 1,
            hasNext: false,
            rerender: false,
			imgListMode:true, //列表显示模式		
			filterShow:false, //是否显示筛选层			
            allCatMenu: [], //总分类
			curPname:'男装', //分类对应的名称
			subCatMenu: [], //子分类
			currentCat:'全部', //默认当前子分类
			showSubMenu: [], //用于显示的数据
			markets: [], //市场
			currentMarket: '全部',			
            showMarket: [], //市场显示数据
            catIsOpen: false, //子分类列表是否展开
            marketIsOpen: false, //市场列表是否展开
            publishTime: [1,2,3], //发布日期
            publishD: '',
		}
	},
	components:{
		commonHead,
		goodsImgList,
		goodsList,
		Loading,
	},
	mixins: [ajax],
	created(){
		this.categoryGoods()
		this.getCatList()
		this.getSubCatList(this.website, this.curPid = 30)
		this.getMarketByPid(this.website, this.curPid = 30)
	},
	computed: {
		...mapState({
			'website': state => state.website,
			'loading': state => state.loading,
		})
	},
	methods:{
		...mapActions(['startLoding']),

        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },
		//获取商品列表
		async categoryGoods(){
			this.loadingShow()

			let options = {
				webSite: this.website,
				keyword: this.keyword,
				pid: this.curPid,
				cid: this.cid,
				orderBy: this.orderType,
				marketId: this.marketId,
				startPrice: this.startPrice,
				endPrice: this.endPrice,
				index: this.pageIndex,
				size: 30,
				d: this.publishD,
				type: 1,
			}
			this.ajax(await querySearchGoods(options), (resp) => {
                if(this.rerender){
                    this.rerender = false
                    this.goodsList = resp.goodsList
                }else{
                    this.goodsList = this.goodsList.concat(resp.goodsList)
                }
                this.hasNext = resp.hasNext
            })

            this.loadingHide()
		},
		//获取总分类列表
		async getCatList(){
			this.ajax(await getAllCats(this.website), (resp) => {
				this.allCatMenu = resp.catGroups
			})
		},
		//获取子分类列表
		async getSubCatList(){
			this.ajax(await getSubCats(this.website, this.curPid), (resp) => {
				this.subCatMenu = resp.subCats
				this.showSubMenu = this.subCatMenu.slice(0, 5)
			})
		},
		//获取市场列表
		async getMarketByPid(){
			this.ajax(await getMarketByPid(this.website, this.curPid), (resp) => {
				this.markets = resp.markets
				this.showMarket = this.markets.slice(0, 5)
			})
		},
		//获取当前菜单
		changOrderBy(type){
			if(type == 'price-asc'){
				this.priceType = 'price-desc'
			}else if(type == 'price-desc'){
				this.priceType = 'price-asc'
			}else{
				this.priceType = ''
			}
			this.orderType = type
			this.rerender = true
			this.categoryGoods()
		},
		//切换列表显示模式
		changeListMode(){
			this.imgListMode = !this.imgListMode
		},
		//显示筛选框
		filterDet(){
			this.filterShow = true
		},
		//点击总分类
		changePid(pid, name){
			this.curPid = pid
			this.curPname = name
			this.currentCat = '全部'
			this.currentMarket = '全部'
			this.getSubCatList()
			this.getMarketByPid()
		},
		//点击子分类
		changeSubCat(cid, name){
			this.cid = cid
			this.currentCat = name
		},
		//子分类展开收起
		openCat(){
			this.catIsOpen = !this.catIsOpen
			if(this.catIsOpen){
				this.showSubMenu = this.subCatMenu.slice(0);
			}else{
				this.showSubMenu = this.subCatMenu.slice(0,5);
			}
		},
		//点击市场
		changeMarket(mid, name){
			this.marketId = mid
			this.currentMarket = name
		},		
		//市场展开收起
		openMarket(){
			this.marketIsOpen = !this.marketIsOpen
			if(this.marketIsOpen){
				this.showMarket = this.markets.slice(0);
			}else{
				this.showMarket = this.markets.slice(0,5);
			}
		},
		//点击发布时间
		getCurrentTime(date){
			this.publishD = date;
		},
		//下一页
		nextPage(){
            this.pageIndex++
            this.categoryGoods()
        },
		//关闭筛选
		filteClose(){
			this.filterShow = false
		},
		//确认筛选
		filterSure(){
			this.filterShow = false
			this.rerender = true
			this.pageName = this.currentCat
			this.categoryGoods()
			this.resetFilterItems()			
		},
		resetFilterItems(){
			//清空数据
			this.keyword = ''
			this.cid = ''
			this.orderBy = 'comp'
			this.marketId = ''
			this.startPrice = ''
			this.endPrice = ''
			this.d = ''

			//设置默认项
			this.currentCat = '全部'
			this.currentMarket = '全部'			
            this.publishD = ''
		}
	}

}
</script>

<style scoped>
.type_menu{position:fixed; background:#fff; width:100%; height:.42rem; top:.48rem; bottom:.48rem; box-shadow:0 1px .06rem rgba(0,0,0,.1); z-index:10;}
.type_menu ul{width:2.5rem; display:inline-flex; }
.type_menu li{flex:1; font-size:.14rem;  text-align:center; height:.42rem; color:#666; overflow:hidden;}
.type_menu li span{display:inline-block; padding:0 .06rem; line-height:.42rem; height:.42rem; border-bottom:.02rem solid #fff;}
.type_menu li.current span{border-color:#333; color:#333;}
.type_menu .icon_change{color:#666; font-size:.16rem; position:relative; top:.01rem; margin-left:.06rem;}
.type_menu .filter{display:block; font-size:.14rem; color:#666; width:.76rem; float:right; text-align:center; line-height: .18rem; border-left:1px solid #d8d8d8; margin-top:.12rem;}
.type_menu .filter i{font-size:.12rem;}

.goods_imglist{margin-top:.98rem; overflow:hidden;}
.goods_list{padding-top:.9rem; background:#fff; overflow:hidden;}

.shade_layer{position:fixed; left:0; top:0; width:100%; height:100%; z-index:1000; background:rgba(0,0,0,.2);}
.filter_layer{position:fixed; right:0; top:0; width:3.06rem; height:100%; z-index:1001;}
.title{background:#f2f2f2; line-height:.36rem; font-size:.12rem; color:#999; padding-left:0.16rem; position:fixed; width:3.06rem; top:0; right:0; height:.36rem; z-index:1003;}
.content{position:fixed; width:3.06rem; right:0; top:.36rem; bottom:.6rem; overflow-y:auto; background:#fff; z-index:1002; -webkit-overflow-scrolling:touch;}
.filter_item{margin:.08rem .15rem; border-bottom:1px solid #e1e1e1; font-size:.12rem; overflow:hidden; padding-bottom:.08rem;}
.filter_item .item_tit{color:#333;}
.filter_item .item_tit em{color:#999; display:inline-block; font-style:normal; margin:0 .06rem; position:relative; top:-.02rem;}
.filter_item .item_tit span{float:right; color:#999;}
.filter_item .item_tit span i{color:#CDCED2; font-size:.11rem; margin-left:.02rem;}
.filter_item .items{margin:.08rem 0;}
.filter_item .items li{width:.9rem; height:.36rem; line-height:.36rem; background:#f3f3f3; border:1px solid #f3f3f3; float:left; margin:1px; text-align:center;}
.filter_item .items li span{display:block;}
.filter_item .items li i{display:none;}
.filter_item .items li.current{color:#f40; background:#fff; border-color:#f40;}
.filter_item .items li.current i{display:inline-block; vertical-align:middle; font-size:.12rem; margin-right:.04rem;}
.filter_item:last-child{border-bottom:0;}
.filter_item .items .hide{display:none;}
.filter_item .items .show{display:block;}
.price_item{border-bottom:0;}
.price_item .price_range{background:#f3f3f3; padding:.05rem; margin:.08rem 0; position:relative; overflow:hidden;}
.price_item .price_range input{border:1px solid #d8d8d8; width:1.20rem; height:.36rem; line-height: .36rem; font-size:.12rem; text-align:center; display:inline-block; margin-right:-4px;}
.price_item .price_range em{display:inline-block; width:.12rem; height:.02rem; background:#d8d8d8; vertical-align:middle; margin:0 .05rem;}

.filter_btns{position:fixed; bottom:0; width:3.06rem; right:0; height:.6rem; display:flex; z-index:1003;}
.filter_btns span{width:50%; height:.6rem; line-height:.6rem; text-align:center; font-size:.16rem;}
.filter_sure{background:#0688de; color:#fff;}
.filter_cancel{background:#fff; color:#999; border-top:1px solid #dedede;}
</style>


