<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '注册'
		}"></common-head>
		<div class="container">
			<div class="form">
				<ul v-if="firstStep">
					<li><input v-model="tel" type="text" pattern="[0-9]*" name="telephone" placeholder="请输入手机号码"></li>
					<li>
						<input v-model="msgCode" type="text" pattern="[0-9]*" name="msgValidate" placeholder="请输入短信验证码">
						<input type="button" :disabled="!!buttonCount" :value="buttonText" @click="getTelCode" class="get_btn">
					</li>
					<li><div class="login_btn" @click="toNext">下一步</div></li>
				</ul>
				
				<ul v-else>
					<li><input v-model="pwd" type="password" name="password" placeholder="请输入密码"></li>
					<li><input v-model="pwdAgain" type="password" name="passwordAgain" placeholder="再次确认密码"></li>
					<li><div class="login_btn" @click="registSure">确定</div></li>
				</ul>

			</div>
		</div>

	</div>
</template>

<script>
import commonHead from '../components/commonHead'
import {msgTip} from '../plugins/util'
import {getTelMsgCode, userRegist, ajax} from '../data/service'
export default{
	data(){
		return{
			firstStep: true,
			tel: '',
			msgCode: '',
			buttonCount: false,
			pwd: '',
			pwdAgain: ''
		}
	},
	components:{
		commonHead
	},
	computed:{
		buttonText(){
			return this.buttonCount ? '重新发送('+ this.buttonCount +')' : '获取验证码'
		}
	},
	mixins:[ajax],
	methods:{
		//获取验证码，在返回成功后调用getMsgCode倒计时
		async getTelCode(){
			if(!this.tel){
				return msgTip('手机号码不能为空')
			}else{
				this.ajax(await getTelMsgCode(this.tel, 2), function(resp){
					this.countDown()
				})
			}			
		},
		//注册接口
		async userRegist(){
			this.ajax(await userRegist(this.tel, this.msgCode, this.pwd), function(resp){
				this.$router.push('/usercenter')
			})
		},
		countDown(){
			this.buttonCount = 60
			let t = setInterval(() => {
				this.buttonCount--
				if(!this.buttonCount){
					clearInterval(t)
				}
			},1000)
		},
		toNext(){
			if(!this.tel){
				return msgTip('手机号码不能为空')				
			}else if(!this.msgCode){
				return msgTip('短信验证码不能为空')				
			}else{
				this.firstStep = false
			}
		},
		registSure(){
			if(!this.pwd){
				return msgTip('密码不能为空')				
			}else if(!this.pwdAgain){
				return msgTip('确认密码不能为空')				
			}else if(this.pwd != this.pwdAgain){
				return msgTip('两次密码不一致')
			}else{
				this.userRegist()
			}
		}
	}
}
</script>

<style scoped>
.container{background:#fff; position:absolute; top:.48rem; left:0; width:100%; height: calc(100% - .48rem);}
.form{padding:.12rem .15rem 0 .15rem;}
.form li{position:relative; margin-top:.12rem; overflow:hidden; border-bottom:1px solid #d8d8d8;}
.form li input{width:100%; height:.4rem; line-height:.4rem; font-size:.16rem;}
.form li .get_btn{position:absolute; right:.12rem; font-size:.14rem; color:#666; top:0; width:.8rem; text-align:right; background:#fff;}
.form li:last-child{border-bottom:0;}
.form li .login_btn{height:.44rem; line-height:.44rem; margin-top:.2rem; color:#fff; font-size:.16rem; text-align:center; background:#0688DE; border-radius:.04rem;}
</style>


