<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '我的奖品'
		}"></common-head>

		<div class="no_acts" v-if="awardsList.length == 0">
			<p>sorry ，目前没有任何活动！尽请期待！</p>
		</div>

		<div class="awards_list" v-else>
			<div class="awards_item" v-for="item in awardsList">
				<div class="award_title">{{item.title}}<span class="award_time">{{item.time}}</span></div>
				<div class="award_info">
					<ul>
						<li v-for="award in item.awardList">
							<img :src="award.icon">
							<span class="text">{{award.text}}</span>
							<span class="state await" v-if="award.state == 1">
								未中奖
							</span>
							<span class="state no_award" v-if="award.state == 2">
								未中奖
							</span>
							<span class="state has_award" v-else-if="award.state == 3 && award.hasReceived == false">
								已中奖<em>领取码：({{award.code}})</em>
							</span>
							<span class="state has_taken" v-else-if="award.state == 3 && award.hasReceived == true">
								已领取
							</span>
						</li>
					</ul>
				</div>

			</div>
		</div>


	</div>
</template>

<script>
import commonHead from '../components/commonHead'
import {getAwardsInfo, ajax} from '../data/service'
export default{
	data(){
		return {
			awardsList: [],
			token: this.$route.query.token ? this.$route.query.token : '',
		}
	},
	created(){
		this.getUserAwardsInfo()
	},
	mixins:[ajax],
	components:{
		commonHead
	},
	methods: {
		async getUserAwardsInfo(){
			this.ajax(await getAwardsInfo(this.token), function(resp){
				this.awardsList = resp.userWinningInfoList
			})
		}
	}
}
</script>

<style scoped>
.no_acts{position:absolute; width:100%; top:.48rem; bottom:0; background:#fff url(../assets/noAct.jpg) no-repeat center .50rem; background-size:50% auto; min-height:2rem; }
.no_acts p{text-align:center; font-size:12px; color:333; padding-top:1.8rem;}

.awards_list{margin-top:.48rem;}
.awards_item{background:#fff; padding:0 .1rem; margin-bottom:.1rem;}
.awards_item .award_title{height:.38rem; line-height:.38rem; font-size:.12rem; color:#ccc;}
.awards_item .award_title .award_time{float:right;}
.awards_item .award_info{}
.awards_item .award_info li{border-top:1px solid #e3e3e3; padding:.05rem 0; display:flex;}
.awards_item .award_info li img{width:.42rem; height:.42rem; border:1px solid #e1e1e1;}
.awards_item .award_info li .text{font-size:.14rem; color:#333; margin-left:.08rem; flex:1; line-height:.42rem; }
.awards_item .award_info li .state{width:1.2rem; font-size:.12rem; text-align:right; line-height:.42rem}
.awards_item .award_info li .await{color:#999;}
.awards_item .award_info li .no_award{color:#ccc;}
.awards_item .award_info li .has_award{color:#f40; padding-top:.04rem; line-height:.2rem}
.awards_item .award_info li .has_award em{display:block; font-size:.1rem; font-style:normal;}
.awards_item .award_info li .has_taken{color:#2cbbf0;}

</style>


