<template>
	<div v-show="loading.status" class="loading">
		<img src="../assets/loading.gif">
		正在加载中
	</div>
</template>

<script>
import {mapState} from 'vuex'
export default{
	data(){
		return {			
		}
	},
	computed:{
		...mapState({
			'loading': state => state.loading,
		})
	}
}
</script>

<style scoped>
.loading{width:1.2rem; height:1.2rem; background:rgba(0,0,0,.7); position:fixed; z-index:20000; left:50%; top:50%; margin-top:-.6rem; margin-left:-.6rem; text-align:center; font-size:.14rem; border-radius:.04rem; color:#eee;}
.loading img{margin:.3rem auto .2rem auto; display:block; width:.32rem;}
</style>


