<template>
	<div>
		<div v-if="needBindTel">
			<common-head :view="{
	            goback: true,
	            pageTitle: '绑定手机号'
	       }"></common-head>
			<div class="container">
				<p class="tip">第一次登录需绑定手机号码</p>
				<div class="form">
					<ul>
						<li><input v-model="telephone" type="text" pattern="[0-9]*" name="telephone" placeholder="请输入手机号码"></li>
						<li>
	                        <input v-model="msgValidate" type="text" pattern="[0-9]*" name="msgValidate" placeholder="请输入短信验证码">
	                        <input :disabled="!!buttonCount" :value="buttonText" type="button" @click="getTelCode()" class="get_btn">
	                    </li>
						<li><div class="login_btn" @click="toNext">下一步</div></li>
					</ul>
				</div>
			</div>
		</div>
		<div v-else class="load_wrap">正在加载</div>
	</div>
</template>

<script>
import commonHead from '../components/commonHead'
import {msgTip} from '../plugins/util'
import {getTelMsgCode, bindTelephone, ajax} from '../data/service'
export default{
	data(){
		return{
			type: this.$route.query.type,
			tempId: this.$route.query.tempId,
			needBindTel: false,
            telephone: '', //手机号码
            msgValidate: '', //验证码
            buttonCount: 0, //倒计时            
		}
	},
	components:{
		commonHead
	},
    computed: {
        buttonText(){
            return this.buttonCount ? '重新发送(' + this.buttonCount +')' : '获取验证码'
        },
    },
    created(){
    	this.isNeedBind()
    },
    mixins:[ajax],
	methods:{
		//判断是否需要绑定手机
		isNeedBind(){			
			if(this.type == 0){
				this.needBindTel = true
			}else{
				this.$router.push('/home')
			}
		},
		//获取验证码，在返回成功后调用getMsgCode倒计时
		async getTelCode(){
			if(!this.telephone){
				return msgTip('手机号码不能为空')
			}else{
				this.ajax(await getTelMsgCode(this.telephone, 4), function(){
					this.countDown()
				})
			}
		},
		//绑定手机接口
		async bindTel(){
			this.ajax(await bindTelephone(this.telephone, this.msgValidate, this.tempId), function(){
				this.$router.push('home')
			})			
		},
		countDown(){
            this.buttonCount = 60;
			let tr = setInterval(() => {
                this.buttonCount --;
                if(!this.buttonCount){
                    clearInterval(tr)
                }
            }, 1000)
		},
		toNext(){
			if(!this.telephone){
				return msgTip('手机号码不能为空')
			}else if(!this.msgValidate){
				return msgTip('短信验证码不能为空')
			}else{
				this.bindTel()
			}
		}
	},
}
</script>

<style scoped>
.container{background:#fff; position:absolute; top:.48rem; left:0; width:100%; height: calc(100% - .48rem);}
.container .tip{font-size:.14rem; text-align:center; color:#666; padding:.3rem 0 .1rem;}
.form{padding:.12rem .15rem 0 .15rem;}
.form li{position:relative; margin-top:.12rem; overflow:hidden; border-bottom:1px solid #d8d8d8;}
.form li input{width:100%; height:.4rem; line-height:.4rem; font-size:.16rem;}
.form li .get_btn{position:absolute; right:.12rem; font-size:.14rem; color:#666; top:0; width:.8rem; text-align:right; background:#fff;}
.form li:last-child{border-bottom:0;}
.form li .login_btn{height:.44rem; line-height:.44rem; margin-top:.2rem; color:#fff; font-size:.16rem; text-align:center; background:#0688DE; border-radius:.04rem;}
.load_wrap{width:100%; height:.4rem; line-height:.4rem; color:#999; text-align:center; font-size:.14rem; position:absolute; top:50%; margin-top:-.2rem;}
</style>


