<template>
  	<div v-if="showCat" class="tb_temp">
        <common-head :view="{
            pageTitle: '上传到淘宝'
        }"></common-head>  
        
        <go-back @backFormChild="backToParent('setCat')"></go-back>
        <span class="updata_temp" @click="updateTbCat">更新分类</span>

        <section class="cat_list" v-if="shopcats.length > 0">
            <div class="cat_item" v-for="cat in shopcats">
                <h3>{{cat.catName}}</h3>
                <ul>
                    <li v-for="subcat  in cat.subCats">
                        <label @click="selected(subcat)" :class="{current: subcat.checked}">
                            <input v-model="catIds" type="checkbox" :value="subcat.scid">
                            {{subcat.catName}}
                        </label>
                    </li>
                </ul>
            </div>
        </section>
        
        <div class="cat_surebtn" @click="tempToParent">确定</div>
    </div>
</template>

<script>
import commonHead from '../components/commonHead'
import goBack from '../page/uploadTbGoBack'
import {getTbCatList, ajax} from '../data/service'

export default {
    data: function(){
    	return{
            shopcats:'',
            catIds: [],
            catItems: [],

    	}
    },
    created(){
        this.getTbCategory()
    },
    mixins:[ajax],
    methods:{
        async getTbCategory(){
            this.ajax(await getTbCatList(), function( reps){
                this.shopcats = reps.shopcats              
            })
        },
        selected(subcat){           
            subcat.checked = !subcat.checked
            if(subcat.checked){
                this.catItems.push(subcat.catName)
            }else{
                let index = this.catItems.indexOf(subcat.catName)
                if(index > -1){
                    this.catItems.splice(index, 1)
                }
            }
        },
        //更新分类
        updateTbCat(){
            this.getTbCategory()
            this.catIds = []
            this.catItems = []
        },
        //向父组件传选中的值
        tempToParent(ids, items){
            ids = this.catIds
            items = this.catItems
            this.$emit('valueFromCatChild', {ids, items})
        },
        backToParent(data){
            this.$emit('hideThisChild', data)
        }
    },
    components:{
        commonHead,
        goBack
    },
    props:['showCat']
}
</script>

<style scoped>
.tb_temp{position:absolute; background:#f2f2f2; z-index:110; width:100%; height:100%; left:0; top:0;}
.updata_temp{position:fixed; font-size:.14rem; color:#333; top:.14rem; right:.12rem; z-index:200;}

.cat_list{position:absolute; width:100%; background:#fff; height:calc(100% - .98rem); left:0; top:.48rem; z-index:20; overflow-y:auto; -webkit-overflow-scrolling:touch;}
.cat_item{}
.cat_item h3{padding:.12rem .12rem .08rem; font-size:.14rem; color:#333;}
.cat_item ul{overflow:hidden;}
.cat_item li{width:1.8rem; height:.36rem; line-height:.36rem; text-align:center;color:#666; font-size:.12rem; overflow:hidden; margin:.05rem 0 0 .05rem; float:left;}
.cat_item li label{display:block; background:#f3f3f3; }
.cat_item li label input{visibility:hidden;}
.cat_item li label.current{background:#169AEF; color:#fff;}

.cat_surebtn{position:fixed; width:100%; height:.5rem; line-height:.5rem; background:#198ADB; left:0; bottom:0; text-align:center; font-size:.16rem; color:#fff; z-index:1000;}

</style>



