<template>
  	<div>
        <common-head class="shop_headtop" :view="{
            goback: true,
            searchBar: 1
        }"></common-head>

        <common-head class="shop_headFixed" v-if="showHead" :view="{
            goback: true,
            pageTitle: pageTitle
        }"></common-head>

        <div v-if="showHead">
            <div class="search-icon">
                <router-link :to="{path:'/search', query:{type:'shop', keywords:''}}"><i class="iconfont icon-search"></i></router-link>
            </div>
            <span slot="collect" class="collect">
                <i class="iconfont" :class="[{selected: stored}, {'icon-s-love': stored}, {'icon-love': !stored}]" @click="doCollectAndCancel"></i>
            </span>
        </div>

        <div class="shop_head" :class="{img1: imgType == 1, img2: imgType == 2, img3: imgType == 3}">
            <div class="shop_info">
                <div class="shop_imgBox">
                    <img :src="shopHeadUrl" alt class="shop_portrait">
                </div>
                <div class="shopInfo">
                    <h1>{{marketName}}{{shopNum}}</h1>
                    <div class="stars_num"><star-num :stars="starNum"></star-num></div>
                    <p>商品总数 {{itemNum}}<span>|</span>今日上新 {{todayAdd}}</p>
                </div>
                <div class="shop_ope">
                    <b :class="{selected: stored}" @click="doCollectAndCancel">
                        <i class="iconfont icon-love" v-if="!stored"></i>
                        <i class="iconfont icon-s-love" v-else></i>收藏
                    </b>
                    <b class="shop_tel" @click="makeACall"><i class="iconfont">&#xe660;</i>电话</b>
                </div>
            </div>
        </div>

        <div class="shop_tfbt_box" :class="{'perch': showHead, 'perch1': !showSort}">
            <div class="shop_tab" :class="{'suspend': showHead}">
                <ul>
                    <li v-for="tab in shopTabs" :class="{selected: tab.type == currentTab}" @click="changeShopTab(tab.type)">
                        <i :class="['iconfont', tab.icon]"></i>
                        <span>{{tab.name}}</span>
                    </li>                    
                    <li>
                        <router-link :to="{path:'/shopCategory', query:{id:shopId, market:marketName, shopNum:shopNum}}">
                            <i class="iconfont">&#xe65d;</i><span>宝贝分类</span>
                        </router-link>
                    </li>
                </ul>
            </div>
            <div class="shop_filt" :class="{'suspend': showHead}" v-if="showSort">
                <ul>
                    <li v-for="item in menus" :class="{selected: sortType == item.type}" @click="sortOrderBy(item.type)"><span>{{item.name}}</span></li>
                    <li :class="{selected: pirceOrder == 'price-asc'}" v-if="pirceOrder == 'price-asc'" @click="sortByPrice('price-asc')"><span>价格</span></li>
                    <li :class="{selected: pirceOrder == 'price-desc'}" v-else @click="sortByPrice('price-desc')"><span>价格</span></li>
                </ul>
            </div>
        </div>
        <div class="goods_list">
            <goods-list showMarket='true' :goods="goodsList" v-if="goodsList.length > 0"></goods-list>
            <div v-else class="no_item"><span></span><p>暂无推荐记录</p></div>
        </div>

        <div v-show="hasNext" @click="nextPage" class="next_page"><span>点击加载更多</span></div>
        
        <loading></loading>
    </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import goodsList from '../components/goodsImgList'
import starNum from '../components/stars'
import Loading from '../components/loading'
import {msgTip} from '../plugins/util'
import {getShopData, querySearchGoods, hasCollected, addCollect, deleteCollect, ajax} from '../data/service'

export default {
    data: function(){
    	return{
            shopId: this.$route.params.id,
    		imgType: '',
            showSort: true,
            pageTitle:'',
            menus: [
                {type:'common', name:'综合'},
                {type:'xp', name:'新品'},
                {type:'popular', name:'人气'}
            ],
            pirceOrder: true,
            showHead: false,
            shopTabs:[
                {type:'common', name:'全部商品', icon:'icon-allgoods'},
                {type:'popular', name:'热卖商品', icon:'icon-hot'},
                {type:'xp', name:'新品上架', icon:'icon-newgoods'}
            ],
            currentTab:'common',
            sortType: 'common',
            coll: false,            
            marketName: '',
            shopNum: '',
            shopHeadUrl: '',
            telphone: '',
            starNum: '',
            itemNum: '',
            todayAdd: '', 
            goodsList: [],
            rerender: false, 
            pageIndex: 1,
            hasNext: false,
            stored: false,
            collectIds: [],
            collectId: '',
    	}
    },
    created(){
        this.getShopInfo()
        this.getGoodsList()
        this.shopIsCollected()
    },
    mounted: function(){
        this.imgType = ~~(Math.random() * 3 + 1);
        window.addEventListener('scroll', this.handleScroll);
    },
    computed:{
        ...mapState({
            'userId': state => state.userId,
            'userType': state => state.userType,
            'website': state => state.website,
            'loading': sate => state.loading,
        })
    },
    mixins:[ajax],
    methods: {
        ...mapActions(['initUserLoginState', 'startLoding']),

        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },
        //获取店铺信息
        async getShopInfo(){
            this.ajax(await getShopData(this.website, this.shopId), (resp) => {
                this.marketName = resp.market
                this.shopNum = resp.shopNum
                this.shopHeadUrl = resp.shopHeadUrl
                this.telephone = resp.telephone
                this.itemNum = resp.itemNum
                this.todayAdd = resp.todayAdd
                this.starNum = resp.starNum
                this.pageTitle = this.marketName + this.shopNum
            })
        },
        //获取商品列表
        async getGoodsList(){
            this.loadingShow()

            let options = {
                webSite: this.website,
                orderBy: this.sortType,
                storeId: this.shopId,
                index: this.pageIndex,
                type: 3,
            }
            this.ajax(await querySearchGoods(options), (resp) => {
                if(this.rerender){
                    this.rerender = false
                    this.goodsList = resp.goodsList
                }else{
                    this.goodsList = this.goodsList.concat(resp.goodsList)
                }
                this.hasNext = resp.hasNext
            })

            this.loadingHide()
        },
        //是否已收藏该店铺
        async shopIsCollected(){
            await this.initUserLoginState()

            if(!this.userId){
                return
            }
            this.ajax(await hasCollected(this.website, 'shop', this.shopId), (resp) => {
                if(resp.collectId){
                    if(this.collectIds.indexOf(resp.collectId) < 0){
                        this.collectIds.push(resp.collectId)
                    }
                    this.collectId = resp.collectId
                    this.stored =  true
                }
            })
        },
        //下一页
        nextPage(){
            this.pageIndex++
            this.getGoodsList()
        },
        //点击排序
        sortOrderBy(type){
            this.sortType = type
            this.rerender = true
            this.pageIndex = 1
            this.pirceOrder = ''
            this.getGoodsList()
        },
        //价格排序
        sortByPrice(type){
            this.rerender = true
            if(type == 'price-asc'){
                this.sortType = 'price-asc'
                this.pirceOrder = 'price-desc'
            }else{
                this.sortType =  'price-desc'
                this.pirceOrder = 'price-asc'
            }
            this.getGoodsList()
        },
        //切换菜单
        changeShopTab(type){
            this.currentTab = this.sortType = type
            this.rerender = true
            if(type != 'common'){
                this.showSort = false;             
            }else{
                this.showSort = true;
            }
            this.getGoodsList()
        },
        //打电话
        makeACall(){
            window.location.href = 'tel:' + this.telephone
        },
        //收藏|取消收藏店铺
        async doCollectAndCancel(){
            
            if(!this.userId){
                this.$router.push('/login')
            }else if(this.userType == '2'){
                return msgTip('供应商不支持此操作', '我知道了')
            }else{
                if(this.stored){
                    this.ajax(await deleteCollect(this.website, 'shop', this.collectIds), (resp) => {
                        this.stored = false
                    })
                }else{
                    this.ajax(await addCollect(this.website, 'shop', this.shopId), (resp) => {
                        this.collectId = resp.collectId
                        if(this.collectIds.indexOf(resp.collectId) < 0){
                            this.collectIds.push(resp.collectId)
                        }
                        this.stored = true
                    })
                }
            }            
        },
        //滚动头部样式变化
        handleScroll(){
            if(window.scrollY > 115){
                this.showHead = true;
            }else{
                this.showHead = false;
            }
        }
    },

    components:{
        commonHead,
        starNum,
        goodsList,
        Loading,
    }
}
</script>

<style>
.shop_headtop .search-bar{width:2.8rem; position:absolute; right:.12rem;}
</style>

<style scoped>
.shop_headFixed{z-index:200;}
.search-icon{position:fixed; right:.44rem; top:.12rem; z-index:300;}
.search-icon i{font-size:.22rem; color:#999;}
.collect{position:fixed; right:.12rem; top:.12rem; z-index:300;}
.collect i{font-size:.24rem; color:#999;}
.collect i.selected{color:#EC2E28;}

.shop_head{width:3.75rem; padding-top:1.62rem; background: red; position:relative; background-size: 3.75rem 1.62rem!important;}
.shop_head.img1{background:url('../assets/1.jpg') no-repeat center center;}
.shop_head.img2{background:url('../assets/2.jpg') no-repeat center center;}
.shop_head.img3{background:url('../assets/3.jpg') no-repeat center center;}

.shop_info{position:absolute; width:3.75rem; height:.86rem; bottom:0; left:0; padding:0;}
.shop_info .shop_imgBox{width:.62rem; height:.62rem; float: left; margin-left: .16rem;}
.shop_info .shop_portrait{width:.62rem; height:.62rem;}
.shop_info .shopInfo{float:left; margin-left: .09rem;}
.shop_info .shopInfo h1{font-size: .16rem; width: 1.32rem; height: .19rem; color: #fff; overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}
.shop_info .shopInfo p{font-size: .12rem; color: #fff; line-height: .14rem; margin-top:.04rem;}
.shop_info .shopInfo p span{margin:0 .09rem;}
.shop_info .shop_ope{float: right; margin-right: .13rem; width:.7rem; margin-top: -.02rem;}
.shop_info .shop_ope b{display: inline-block; width: .68rem; height: .26rem; line-height: .24rem; border-radius: .02rem; margin-bottom: .1rem; text-align: center; font-size: .12rem; color: #fff; border:1px solid #fff;}
.shop_info .shop_ope b i{margin-right:.04rem; vertical-align: -.01rem; color:#fff;}
.shop_info .shop_ope b.shop_tel{background:#50c800; border-color: #50c800;}
.shop_info .shop_ope b.selected{background: #198adb; border-color:#198adb;}

.goods_list{overflow:hidden; margin-top:.06rem;}
.no_item{text-align:center; padding-top:.7rem;}
.no_item span{display:inline-block; width:.96rem; height:.75rem; background:url(../assets/noItem.png) no-repeat 0 0 / 100% 100%;}
.no_item p{font-size:.14rem; color:#d8d8d8; margin-top:.1rem;}

.shop_tab{background:#fff; width:100%; height:.6rem; padding-top:.14rem; border-bottom: 1px solid #d8d8d8;}
.shop_tab ul{display:flex;}
.shop_tab ul li{flex:1; text-align:center; font-size:.12rem;}
.shop_tab ul li i{display:block; color:#d8d8d8; font-size:.18rem; height:.2rem; margin-bottom:.03rem}
.shop_tab ul li:nth-child(2) i{font-size:.22rem;}
.shop_tab ul li span{color:#333; display:inline-block; height:.23rem; padding:0 .06rem;}
.shop_tab ul li.selected i,
.shop_tab ul li.selected span{color:#ec2e28;}
.shop_tab ul li.selected span{border-bottom: 1px solid #f40;}
.shop_tab ul li a{display:block;}
.shop_filt{background: #fff; width:100%; height:.36rem; line-height: .36rem; color:#333; margin-bottom:.07rem;}
.shop_filt ul{display: flex;}
.shop_filt ul li{flex: 1; text-align: center; font-size: .12rem;}
.shop_filt ul li.selected span{color:#ec2e28;}

.shop_tfbt_box{min-height:.6rem;}
.shop_tfbt_box.perch{min-height:.96rem;}
.shop_tfbt_box.perch.perch1{min-height:.6rem;}

.suspend{position:fixed;}
.shop_tab.suspend{top:.45rem; z-index:50;}
.shop_filt.suspend{top:1.05rem; box-shadow: 0 1px 0.1rem rgba(0,0,0,.1);}
</style>



