<template>
  	<div v-if="showCase" class="tb_temp">
        <common-head :view="{
            pageTitle: '橱窗推荐'
        }"></common-head>  
        
        <go-back @backFormChild="backToParent('setCase')"></go-back>
        
        <p class="case_num">您现在有{{caseTotal}}个橱窗，已使用{{caseUsed}}个橱窗，剩余{{caseLast}}个</p>

        <section class="set_list">
            <ul>
                <li @click="tempToParent(true)"><span>加入</span><em class="iconfont icon-right"></em></li>
                <li @click="tempToParent(false)"><span>不加入</span><em class="iconfont icon-right"></em></li>        
            </ul>
        </section>

    </div>
</template>

<script>
import commonHead from '../components/commonHead'
import goBack from '../page/uploadTbGoBack'
import {getTbCaseInfo, ajax} from '../data/service'

export default {
    data: function(){
    	return{
            caseTotal: '',
            caseUsed: '',
            caseLast: '',
    	}
    },
    created(){
        this.getCaseInfo()
    },
    mixins:[ajax],    
    methods:{
        async getCaseInfo(){
            this.ajax(await getTbCaseInfo(), function(resp){
                this.caseTotal = resp.total
                this.caseUsed = resp.used
                this.caseLast = resp.total - resp.used
            })
        },
        //向父组件传选中的值
        tempToParent(temp){
            this.$emit('valueFromCaseChild', temp)
        },
        backToParent(data){
            this.$emit('hideThisChild', data)
        }
    },    
    components:{
        commonHead,
        goBack
    },
    props:['showCase']
}
</script>

<style scoped>
.tb_temp{position:absolute; background:#f2f2f2; z-index:110; width:100%; height:100%; left:0; top:0;}
.case_num{padding:.06rem 0; text-align:center; margin-top:.56rem; font-size:.14rem; color:#999;}

.set_list{ background:#fff}
.set_list ul{margin-top:.15rem; background:#fff; padding:0 .14rem;}
.set_list li{border-bottom:1px solid #d8d8d8; padding:.1rem 0; font-size:.16rem; color:#333; overflow:hidden;}
.set_list li a{display:block;}
.set_list li span{line-height:.32rem;}
.set_list li em{float:right; color:#d8d8d8; font-size:.2rem; position:relative; top:.03rem; right:-.04rem;}
.set_list ul li:last-child{border-bottom:0;}
</style>



