

export var cookie = function(){
    
    var $doc = document;
 
    return $cookie;
 
    function $cookie(name, value, option){
        
        if(typeof name == 'object'){ //ÅúÁ¿ÉèÖÃcookie
            
            for(var e in name){
                $cookie(e, name[e], value);
            }
            
        }else if(value !== undefined){
            
            option = option || {};
            if(value === null){ //valueÎªnull,ÔòÉèÖÃcookieÎª¹ýÆÚ
                option.expires = -1;
            }else if(typeof option == 'number'){
                option = {expires: option};
            }
            
            var newvalue = name + "=" + encodeURIComponent(value);
            
            if(option.expires){
                var exp = new Date;
                exp.setTime(+exp + option.expires * 60 * 1000); //ÒÔ·ÖÖÓÎªµ¥Î»
                newvalue += "; expires=" + exp.toUTCString();
            }
            
            if(option.path){
                newvalue += "; path=" + option.path;
            }
            
            if(option.domain){
                newvalue += "; domain=" + option.domain;
            }
            
            $doc.cookie = newvalue;
            
        }else{
            return (value = $doc.cookie.match('(?:^|;)\\s*' + name + '=([^;]*)'))
                ? decodeURIComponent(value[1]) : null;
        }
        
    }
    
}()

export var appCookie = function(name, value){
    return cookie(name, value, {
        expires: 60 * 24 * 356 //ÓÐÐ§ÆÚÒ»Äê
    });
}

export var msgTip = function(msg, btnText){
    var hasLayer = document.getElementById('tipLayer');
    var text = btnText ? btnText : '确定'
    if(hasLayer){
        document.getElementById('tipMsg').innerHTML = msg
    }else{
        var tipLayer = document.createElement('div');
        tipLayer.setAttribute('id', 'tipLayer');
        tipLayer.className = 'tip_layer';
        tipLayer.innerHTML = `<div class="tip_box">
                                <i class="iconfont icon-k-close tip_close"></i>
                                <div class="tip_msg" id="tipMsg">${msg}</div>
                                <div class="close_btn tip_close">${text}</div>
                            </div>`
        document.getElementById('app').appendChild(tipLayer);
        var closes = document.getElementsByClassName('tip_close');
        for(var i = 0; i < closes.length; i++){
            closes[i].onclick = function(){
                closeMsgTip(this)
            }
        }
    }
}

export var closeMsgTip = function(obj){
    var tipLayer = document.getElementById('tipLayer');
    var parent = obj.parentNode.parentNode.parentNode;
    parent.removeChild(tipLayer);
}




