<template>
    <header class="common-head">
        <span v-if="view.goback" @click="$router.go(-1)" class="goback"><i class="iconfont icon-left"></i>返回</span>
        
        <h2 v-if="view.pageTitle" class="pageTitle">{{view.pageTitle}}</h2>
        
        <span v-if="view.selectWebsite" class="select-website">
            <!--<router-link to="/cities">-->
                <i class="iconfont icon-addr"></i>
                {{targetWebsiteName}}
                <i class="iconfont icon-downarrow"></i>
            <!--</router-link>-->
        </span>
        
        <div v-if="view.searchBar" class="search-bar" :class="{'search-type': view.searchBar == 2}">
            <div class="type" v-if="view.searchBar == 2">
                <span class="cur_type" @click="toggleVisibleSearchTypeList">{{curSearchTypeName}}<i class="iconfont icon-tringle"></i></span>
                <div v-show="isShowSearchTypeList" class="type_list">
                    <em class="icon icon"></em>
                    <span v-for="item in searchTypeList" @click="selectSearchType(item.id)" :class="item.id">
                        <i :class="'icon-' + item.icon" class="iconfont"></i>{{item.name}}
                    </span>
                </div>
            </div>
            <form action>
                <input type="search" v-model="searchKeywords" autocomplete="off" placeholder="请输入关键词搜索" id="searchInput" class="search-input" @keypress.enter="search">
            </form>
        </div>
        
        <div v-if="view.togglePageType" class="toggle-pageType" @click="togglePageType"><i class="iconfont icon-change"></i><span>{{targetPageTypeName}}</span></div> 

        <div  v-if="view.registEntry" class="regist"><router-link to="/regist">注册</router-link></div>

        <div v-if="view.searchIcon" class="search-icon"><router-link :to="{path:'/search', query:{type:'shop', keywords:'', mode:'icon'}}"><i class="iconfont icon-search"></i></router-link></div>
       
    </header>
</template>

<script>

import {mapState, mapMutations, mapActions, mapGetters} from 'vuex'
import {msgTip} from '../plugins/util'
export default {

    data(){
        return {
            curSearchType: 'goods',
            searchTypeList: [
                {id:'goods', icon:'bag', name:'商品'},
				{id:'shop', icon:'shop', name:'店铺'},
            ],
            searchTypeMap: {},
            isShowSearchTypeList: false,
            searchKeywords:'',
        }
    },
    props: ['view'],    
    created(){
        if(this.view.selectWebsite){
            this.initWebsite()
        }
        this.getSearchData()
    },
    
    mounted(){
        this.curSearchType = this.$route.query.type ? this.$route.query.type : 'goods'
        this.searchKeywords = this.$route.query.keywords ? this.$route.query.keywords : '' 
    },
    
    methods: {
        ...mapMutations([
            'togglePageType',
        ]),
        ...mapActions([
            'initWebsite', 
        ]),
        toggleVisibleSearchTypeList(){
            this.isShowSearchTypeList = !this.isShowSearchTypeList;
        },
        selectSearchType(searchType){
            if(this.curSearchType != searchType){
                this.curSearchType = searchType
            }
            this.toggleVisibleSearchTypeList()
        },
        getSearchData(){
            if(this.view.searchBar == 2){
                let searchTypeMap = {}
                this.searchTypeList.forEach(item => {
                    searchTypeMap[item.id] =  item.name
                })
                this.searchTypeMap = searchTypeMap
            }
        },
        search(){     
            let keywords = this.searchKeywords.replace(/(^\s*)|(\s*$)/g, '')
            if(keywords.length > 0){
                this.$router.push({path:'/search', query:{type:this.curSearchType, keywords:keywords}}) 

            }else{
                msgTip('请输入关键词')
            }
        },
    },
    
    computed: {
        ...mapState([]),
        ...mapGetters([
            'targetPageTypeName',
            'targetWebsiteName',
        ]),
        curSearchTypeName(){
            return this.searchTypeMap[this.curSearchType]
        }
    }
}
</script>
<style>
.search-bar{width:2.32rem; height:.32rem; padding-top:.02rem; display:inline-flex;}  
</style> 
<style scoped>
.common-head{width:100%; height:.48rem; position:fixed; top:0; left:0; background:#fff; z-index:100; padding-top:.06rem; margin-bottom:.06rem; box-shadow:0 1px .06rem rgba(0,0,0,.1); display:flex;}

.select-website{font-size:.16rem; margin-left:.06rem; margin-top:.06rem; width:.72rem;}
.select-website i{font-size:.16rem; margin-right:.04rem;}
 
.search-bar form{flex:1;}
.search-input{width:100%; height:.32rem; line-height:.32rem; font-size:.14rem; background:#fff; border-radius:.06rem; padding-left:.06rem;box-shadow:0 0 6px rgba(0,0,0,.2);}
.search-input[type="search"]::-webkit-search-cancel-button{display:none;}

.search-type{flex:1; background:#f2f2f2; border-radius:.08rem; height:0.34rem;}
.search-type .type{display:block; width:.4rem; margin-right:.04rem;  height:.32rem; text-align:right; padding-right:.08rem; margin-left:.08rem; font-size:.14rem; position:relative; top:.01rem;}
.search-type input{background:#f2f2f2; box-shadow:0 0 0 #fff;}
.search-type .cur_type{line-height:.32rem;}
.search-type .cur_type i{width:.06rem; height:.06rem; font-size:.06rem; position:absolute; top:.03rem; right:0; color:#666; }
.search-type .type_list{position:absolute; background:#666; width:.88rem; height:1rem; left:-.08rem; top:.38rem; color:#fff; padding:.12rem 0;}
.search-type .type_list.show{display:block;}
.search-type .type_list em{position:absolute; width:0; height:0; border-right:10px solid transparent; border-left:10px solid transparent; border-bottom:10px solid #666; top:-.08rem; left:.18rem;}
.search-type .type_list span{display:block; text-align:center; line-height:.34rem;}
.search-type .type_list span i{margin-right:.06rem; font-size:.16rem;}

.toggle-pageType{text-align:center; margin-top:-.02rem; width:.54rem;color:#333;}
.toggle-pageType i{font-size:.24rem; }
.toggle-pageType span{display:block; font-size:.12rem; margin-top:-.06rem;}

.goback{font-size:.14rem; position:absolute; left:.08rem; top:.11rem;}
.goback i{font-size:.2rem; position:relative; top:.02rem; color:#666;}

.pageTitle{width:100%; text-align:center; font-size:.2rem; line-height:.4rem; color:#333;}

.regist{position:absolute; right:.12rem; font-size:.14rem; top:.15rem;}

.search-icon{position:absolute; right:.12rem; top:.12rem;}
.search-icon i{font-size:.18rem;}
</style>


