<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '收藏夹'
		}"></common-head>
		<div class="head_edit" v-if="currentMenu == 'goods' && storeGoods.length > 0">
			<span v-if="goodsEditStatus == 'finish'" @click="editGoodsList('edit')">编辑</span>
			<span v-else @click="editGoodsList('finish')">完成</span>
		</div>
		<div class="head_edit" v-else-if="currentMenu == 'shop' && storeShops.length > 0">
			<span v-if="shopEditStatus == 'finish'" @click="editShopList('edit')">编辑</span>
			<span v-else @click="editShopList('finish')">完成</span>
		</div>

		<div class="store_menu">
			<ul>
				<li><span @click="getCurrentMenu('goods')" :class="{current: currentMenu == 'goods'}">收藏的商品</span></li>
				<li><span @click="getCurrentMenu('shop')" :class="{current: currentMenu == 'shop'}">收藏的档口</span></li>
			</ul>
		</div>
		
		<div class="no_content" v-if="storeGoods.length == 0 && currentMenu == 'goods'">
			<i class="iconfont icon-s-store"></i>
			<p>暂无收藏的商品</p>
			<router-link to="/newgoods">去逛逛商品</router-link>
		</div>
		<div class="no_content" v-else-if="storeShops.length == 0 && currentMenu == 'shop'">
			<i class="iconfont icon-s-store"></i>
			<p>暂无收藏的档口</p>
		</div>
		
		<div :class="['goods_list', {edit: goodsEditStatus == 'edit'}]" v-if="currentMenu == 'goods' && storeGoods.length > 0">
			<ul>
				<li v-for="item in storeGoods">
					<span class="edit_check"><input type="checkbox" name="goods" :value="item.goodsId" v-model="checkedGoodsIds"></span>
		            <router-link :to="{name:'goods', params:{id:item.goodsId}}">
		                <img :src="item.imgsrc">                    
		                <div class="info">
		                    <h3>{{item.title}}</h3>
		                    <span class="price"><i>¥</i>{{item.piprice}}</span>
		                    <span class="market">{{item.fullStoreName}}</span>
		                </div>
		            </router-link>
        		</li>
			</ul>
		</div>

		<div :class="['shop_list', {edit: shopEditStatus == 'edit'}]" v-if="currentMenu == 'shop' && storeShops.length > 0">
			<ul>
				<li v-for="item in storeShops">
					<span class="edit_check"><input type="checkbox" name="shops" :value="item.shopId" v-model="checkedShopsIds"></span>
		            <router-link :to="{name:'shop', params:{id:item.shopId}}">
		                <img :src="item.shopHeadUrl">                    
		                <div class="info">
		                    <h3>{{item.market}}{{item.shopNum}}</h3>
		                    <star-num :stars="item.starNum"  class="stars"></star-num>
		                    <span class="main_case">主营：{{item.mainCase}}</span>
		                </div>
		            </router-link>
					<div class="contact">
						<i class="iconfont icon-phone" @click="phoneCall(item.telephone)"></i>
						<span>共{{item.itemNum}}件宝贝</span>
					</div>
				</li>
			</ul>
		</div>
	
		<div v-show="goodsHasNext && currentMenu =='goods'" @click="goodsNextPage" class="next_page"><span>点击加载更多</span></div>
		<div v-show="shopHasNext && currentMenu =='shop'" @click="shopNextPage" class="next_page"><span>点击加载更多</span></div>

		<div class="all_delete" v-if="currentMenu == 'goods' && goodsEditStatus == 'edit' && checkedGoodsIds.length > 0" @click="deleteCheckedStoreGoods">全部删除</div>
		<div class="all_delete" v-if="currentMenu == 'shop' && shopEditStatus == 'edit' && checkedShopsIds.length > 0" @click="deleteCheckedStoreShop">全部删除</div>
		
		<loading></loading>

	</div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import starNum from '../components/stars'
import Loading from '../components/loading'
import {getStoreGoodsList, getStoreShopsList, deleteCollect, ajax} from '../data/service'
import {msgTip} from '../plugins/util'

export default{
	data(){
		return{
			goodsEditStatus: 'finish',
			shopEditStatus: 'finish',
			currentMenu: 'goods',
			goodsIndex: 1,
			goodsSize: 30,
			shopIndex: 1,
			shopSize: 30,
			storeGoods: [],
			storeShops: [],
			checkedGoodsIds: [],
			checkedShopsIds: [],
			rerenderGoods: false,
			rerenderShop: false,
			goodsHasNext: false,
			shopHasNext: false
		}
	},
	created(){
		this.getStoreGoods()
	},
	mixins:[ajax],
	computed:{
		...mapState({
			'website': state => state.website,
			'loading': state => state.loading,
		})
	},
	methods: {
		...mapActions(['startLoding']),
		loadingShow(){
			this.startLoding({status:true})
		},
		loadingHide(){
			this.startLoding({status:false})
		},
		//获取收藏商品列表
		async getStoreGoods(){
			this.loadingShow()

			this.ajax(await getStoreGoodsList(this.website, this.goodsIndex, this.goodsSize),function(reps){
				if(this.rerenderGoods){
					this.rerenderGoods = false
					this.storeGoods = reps.items
				}else{
					this.storeGoods = this.storeGoods.concat(reps.items)	
				}
				this.goodsHasNext = reps.hasNext
				
			})

			this.loadingHide()
		},
		//获取收藏店铺列表
		async getStoreShops(){
			this.loadingShow()

			this.ajax(await getStoreShopsList(this.website, this.shopIndex, this.shopSize),function(reps){
				if(this.rerenderShop){
					this.rerenderShop = false
					this.storeShops = reps.shops
				}else{
					this.storeShops = this.storeShops.concat(reps.shops)
				}
				this.shopHasNext = reps.hasNext
				
			})

			this.loadingHide()
		},		
		//删除收藏商品
		async deleteStoreGoods(){
			this.ajax(await deleteCollect(this.website, 'goods', this.checkedGoodsIds),function(resp){
				this.goodsIndex = 1
				this.getStoreGoods()
			})
		},
		//删除收藏店铺
		async deleteStoreShop(){
			this.ajax(await deleteCollect(this.website, 'shop', this.checkedShopsIds),function(resp){
				this.shopIndex = 1
				this.getStoreShops()
			})
		},
		//加载更多商品
		goodsNextPage(){
			this.goodsIndex++
			this.getStoreGoods()
		},
		//加载更多店铺
		shopNextPage(){
			this.shopIndex++
			this.getStoreShops()
		},
		//获取当前是显示商品还是档口
		getCurrentMenu(type){
			this.currentMenu = type
			if(type == 'goods'){
				this.getStoreGoods()
			}else{
				this.getStoreShops()
			}
		},
		//收藏的商品编辑状态
		editGoodsList(status){
			this.goodsEditStatus = status
			if(status == 'edit'){
				this.goodsHasNext = false
			}else{
				this.goodsHasNext = true
			}	
		},
		//收藏的档口编辑状态
		editShopList(status){
			this.shopEditStatus = status
			if(status == 'edit'){
				this.shopHasNext = false
			}else{
				this.shopHasNext = true
			}	
		},		
		//从收藏中删除选中的商品
		deleteCheckedStoreGoods(){
			this.deleteStoreGoods()
			this.goodsEditStatus = 'finish'

		},
		//从收藏中删除选中的档口
		deleteCheckedStoreShop(){
			this.deleteStoreShop()
			this.shopEditStatus = 'finish'
		},
		//拨打电话
		phoneCall(phone){
			window.location.href = 'tel:' + phone
		}
	},
	components:{
		commonHead,
		starNum,
		Loading,
	}
}
</script>

<style scoped>
.head_edit{position:fixed; right:.1rem; font-size:.16rem; top:.12rem; z-index:10005;}

.store_menu{position:fixed; background:#fff; width:100%; height:.42rem; top:.48rem; bottom:.48rem; box-shadow:0 1px .06rem rgba(0,0,0,.1); z-index:10;}
.store_menu ul{width:100%; display:flex; }
.store_menu li{flex:1; font-size:.14rem;  text-align:center; height:.42rem; color:#666; overflow:hidden;}
.store_menu li span{display:inline-block; padding:0 .06rem; line-height:.42rem; height:.42rem; border-bottom:.02rem solid #fff;}
.store_menu li span.current{border-color:#333; color:#333;}

.no_content{margin-top:.89rem; text-align:center; padding-top:1rem;}
.no_content i{font-size:60px; color:#ccc;}
.no_content p{font-size:.13rem; color:#ccc; margin:.3rem 0 .2rem 0;}
.no_content a{display:inline-block; width:.96rem; height:.3rem; line-height:.3rem; color:#fff; font-size:.14rem; text-align:center; background:#198ADB; border-radius:.15rem;}

.goods_list,
.shop_list{background:#fff; padding-top:.89rem; padding-bottom:.14rem;}
.goods_list li,
.shop_list li{padding-left:.12rem; margin-top:.14rem; overflow:hidden; display:flex;}
.goods_list li .edit_check,
.shop_list li .edit_check{width:.38rem; text-align:left; display:none;}
.goods_list li .edit_check input,
.shop_list li .edit_check input{width:.2rem; height:.2rem; margin-top:.38rem; background:url(../assets/check.png) no-repeat 0 0; background-size:100% 100%;}
.goods_list li .edit_check input:checked,
.shop_list li .edit_check input:checked{background-image:url(../assets/checked.png); }
.goods_list li a,
.shop_list li a{flex:1; display:inline-flex;}
.goods_list li img{width:.96rem; height:.96rem; margin-right:.12rem;}
.goods_list li .info{flex:1; border-bottom:1px solid #d8d8d8;}
.goods_list li .info h3{font-size:.12rem; color:#333; line-height:.2rem; padding-right:.06rem; overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:2;-webkit-box-orient:vertical; margin-bottom:.24rem;}
.goods_list li .info .price{float:left; color:#f40; font-size:.16rem;}
.goods_list li .price i{font-size:.12rem; margin-right:.02rem;}
.goods_list li .market{float:right; color:#999; font-size:.12rem; margin-right:.08rem; position:relative; top:.04rem;}
.goods_list.edit,
.shop_list.edit{padding-bottom:.6rem}
.goods_list.edit li .edit_check,
.shop_list.edit li .edit_check{display:block;}

.shop_list li{padding-right:.06rem; border-bottom:1px solid #d8d8d8; padding-bottom:.14rem; padding-left:0; margin-left:.12rem; margin-right:.12rem;}
.shop_list li .edit_check input{margin-top:.2rem;}
.shop_list li img{width:.62rem; height:.62rem; margin-right:.1rem;}
.shop_list li .info{position:relative; height:.62rem; overflow:hidden;}
.shop_list li .info h3{width:1.8rem; font-size:.14rem; color:#333; overflow:hidden; white-space:nowrap; text-overflow:ellipsis;}
.shop_list li .info .stars{margin-top:-.02rem;}
.shop_list li .info .main_case{position:absolute; font-size:.12rem; color:#999; left:0; bottom:0;}
.shop_list li .contact{width:.7rem; text-align:center;}
.shop_list li .contact i{font-size:.3rem; color:#333; display:block; margin-top:.06rem;}
.shop_list li .contact span{display:block; color:#999; font-size:.12rem; margin-top:.04rem;}


.all_delete{width:100%; height:.5rem; line-height:.5rem; position:fixed; left:0; bottom:0; background:#198ADB; font-size:.16rem; color:#fff; text-align:center;}
</style>


