<template>
  	<div>
        <common-head :view="{
            goback: true,
            searchIcon: true
        }"></common-head>

        <div class="goodsMenu">
            <ul>
                <li v-for="item in menus" @click="changeMenu(item)" :class="{current: currentMenu == item}"><span>{{item}}</span></li>
            </ul>
        </div>

        <div class="content">
            <transition-group name="fade-in">
                <div class="goods_base" v-if="showBase" key="base">
                    <div class="goods_imgs">
                        <div class="swiper-container" id="bannerSwiper">
                            <div class="swiper-wrapper">
                                <div class="swiper-slide" v-for="img in imgSrcs">
                                   <img :src="img">
                                </div>
                            </div>
                            <div class="swiper-pagination" id="bannerPagination"></div>
                        </div>
                    </div>
                    <div class="goods_info">
                        <h3>{{title}}</h3>
                        <div class="goods_box">
                            <span class="price"><em>￥</em>{{price}}</span>
                            <span class="address">浙江-杭州</span>
                        </div>
                        <div class="goods_box">
                            <span class="goods_num">商品货号：{{goodsNo}}</span>
                            <span class="create_time">发布时间：{{createTime}}</span>
                        </div>
                        <div class="goods_license">
                            <ul>
                                <li v-for="item in goodsLicenses"><i class="iconfont icon-check"></i><span>{{item == 1 ? '可退' : '可换'}}</span></li>
                            </ul>
                        </div>
                    </div>
                    <div class="goods_attrs" @click="showAttrsLayer">
                        选择颜色 尺码
                        <i class="iconfont icon-right"></i>
                    </div>
                    <div class="goods_store">
                        <div class="store_info">
                            <img :src="shopHeadUrl">
                            <h3>{{market}}{{storeNum}}<star-num :stars="starNum" class="stars"></star-num></h3>
                            
                        </div>
                        <router-link :to="{name:'shop', params:{id:storeId}}" class="to_shop">进入店铺</router-link>
                    </div>
                    <div class="detail_more" @click="showGoodsDetail"><span>点击查看商品详情</span></div>
                </div>

                <div class="goods_detail" v-if="showDetail" id="goodsDetail" key="detail" @touchmove="showTopIcon">
                    <div v-html="details">{{details}}</div>
                    <div class="to_top" v-if="isScroll" @click="gotoTop">
                        <i class="iconfont icon-upload"></i>
                    </div>
                </div>

                <div class="goods_attrbox" v-if="showAttrs" key="attrs">
                    <ul>
                        <li v-for="attr in itemKvs"><label>{{attr.key}}</label><span>{{attr.value}}</span></li>
                    </ul>
                </div>
            </transition-group>
        </div>
        
        <!--选择颜色尺码-->
        <div class="sdk_shade" v-if="showSdk" @touchmove.prevent></div>
        <div class="sdk_layer" v-if="showSdk">
            <i class="iconfont icon-close" @click="closeSdkLayer"></i>
            <div class="goods_baseinfo">
                <img :src="imgSrcs[0]">
                <div class="goods_basebox">
                    <span class="price"><em>￥</em>{{price}}</span>
                    <span class="goods_num">商品货号：{{goodsNo}}</span>
                </div>
            </div>
            <div class="sdk_list">
                <div class="sdk_item sdk_size">
                    <h3>商品尺码</h3>
                    <ul>
                        <li v-for='size in size'>{{size}}</li>
                    </ul>
                </div>
                <div class="sdk_item">
                    <h3>商品颜色</h3>
                    <ul>
                        <li v-for='color in colors'>{{color}}</li>
                    </ul>
                </div>
            </div>
        </div>

        <div class="foot">
            <span class="store stored" v-if="isStored" @click="cancelStoreGoods"><i class="iconfont icon-s-store"></i>取消收藏</span>
            <span class="store" v-else @click="storeGoods"><i class="iconfont icon-store"></i>收藏</span>
            <!--<span class="upload_taobao" @click="uploadToTaobao(goodsId)">上传到淘宝</span>-->
            <span class="upload_taobao">上传到淘宝</span>
        </div>

        <loading></loading>
    </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import '../plugins/swiper.min.js'
import '../style/swiper.min.css'
import commonHead from '../components/commonHead'
import starNum from '../components/stars'
import Loading from  '../components/loading'
import {msgTip} from '../plugins/util'
import {getGoodsDetail, addCollect, deleteCollect, hasCollected, ajax} from '../data/service'

export default {
    data(){
    	return{
    		menus: ['商品','详情','参数'],
            currentMenu: '', //当前导航
            showBase: true, //商品
            showDetail: false, //详情
            showAttrs: false, //参数
            showSdk: false, //颜色尺码
            isScroll: false, //详情scrollTop>0
            goodsId: this.$route.params.id,
            title: '',
            imgSrcs: [],
            goodsNo: '',
            createTime: '',
            goodsLicenses: [],
            price: '',
            storeId: '',
            market: '',
            storeNum: '',
            starNum: '',
            shopHeadUrl: '',
            colors:[],
            size:[],
            itemKvs:[],
            details:'',
            type:'goods',
            collectIds:[],
            collectId: '',
            isStored: false,
    	}
    },    

    mounted(){
        setTimeout(function(){
            new Swiper('#bannerSwiper', {
                pagination: '#bannerPagination',
                loop: true
            });
        });
    },
    components:{
        commonHead,
        starNum,
        Loading,
    },
    created(){
        this.getGoodsInfo()
        this.shopIsCollected()
    },
    computed:{
        ...mapState({
            'userId': state => state.userId,
            'userType': state => state.userType,
            'website': state => state.website,
            'loading': state => state.loading,
        }),        
    },
    mixins:[ajax],
    methods:{
        ...mapActions(['initUserLoginState', 'startLoding']),

        loadingShow(){
            this.startLoding({status:true})       
        },
        loadingHide(){
            this.startLoding({status:false})
        },
        //获取商品数据
        async getGoodsInfo(){
            this.loadingShow()

            var that = this
            var resp = await getGoodsDetail(this.website, this.goodsId);
            if(resp.success){
                this.title = resp.title
                this.imgSrcs = resp.imgSrcs
                this.goodsNo = resp.goodsNo
                this.createTime = resp.createTime
                this.goodsLicenses = resp.goodsLicenses
                this.price = resp.price
                this.storeId = resp.storeId
                this.market = resp.market
                this.storeNum = resp.storeNum
                this.starNum = resp.starNum
                this.shopHeadUrl = resp.shopHeadUrl
                this.colors = resp.colors
                this.size = resp.size
                this.itemKvs = resp.itemKvs
                this.details = resp.details
            }else{
                if(resp.nogoods){                    
                    that.$router.go(-1)                   
                }else{
                    msgTip(resp.msg)
                }
            }
            this.loadingHide()
        },
        //是否已收藏该商品
        async shopIsCollected(){
            await this.initUserLoginState()

            if(!this.userId){
                return
            }
            this.ajax(await hasCollected(this.website, 'goods', this.goodsId), (resp) => {
                if(resp.collectId){
                    if(this.collectIds.indexOf(resp.collectId) < 0){
                        this.collectIds.push(resp.collectId)
                    }
                    this.collectId = resp.collectId
                    this.isStored =  true
                }
            })
        },

        //添加收藏
        async storeGoods(){
            if(!this.userId){
                this.$router.push('/login')
            }else if(this.userType == 2){
                return msgTip('供应商不支持此操作', '我知道了')
            }else{
                this.ajax(await addCollect(this.website, this.type, this.goodsId), function(resp){
                    if(resp.success == true){
                        if(this.collectIds.indexOf(resp.collectId) < 0){
                            this.collectIds.push(resp.collectId)
                        }
                        this.collectId = resp.collectId
                        this.storeTip('收藏成功')
                        this.isStored = true;
                    }else{
                        this.storeTip('收藏失败')
                        this.isStored = false;
                    }
                })
            }
        },
        //取消收藏
        async cancelStoreGoods(){            
            this.ajax(await deleteCollect(this.website, this.type, this.collectIds), function(resp){
                if(resp.success == true){
                    this.storeTip('取消成功')
                    this.isStored = false
                }else{
                    msgTip('取消失败')
                    this.isStored = true
                }
            })
        },
        //上传到淘宝判断
        async uploadToTaobao(id){
            if(!this.userId){
                this.$router.push('/login')
            }else{ 
                this.$router.push({name:'upload', params:{id: id}});
            }            
        },
        //菜单切换
        changeMenu(item){
            this.currentMenu = item
            if(item == '商品'){
                this.showBase = true
                this.showDetail = this.showAttrs = false
            }else if(item == '详情'){
                this.showDetail = true
                this.showBase = this.showAttrs = false
            }else{
                this.showAttrs = true
                this.showBase = this.showDetail = false
            } 
        },
        //选择颜色尺码
        showAttrsLayer(){
            this.showSdk = true
        },
        //点击查看详情
        showGoodsDetail(){
            this.currentMenu = '详情'
            this.showDetail = true
            this.showBase = this.showAttrs = false
        },
        //关闭尺码颜色弹出框
        closeSdkLayer(){
            this.showSdk = false
        },
        //返回顶部
        gotoTop(){
            document.getElementById('goodsDetail').scrollTop = 0
            this.isScroll = false
        },
        //显示返回顶部按钮
        showTopIcon(){
            let top = document.getElementById('goodsDetail').scrollTop
            if(top > 0){
                this.isScroll = true
            }else{
                this.isScroll = false
            }
        },
        //收藏提示
        storeTip(msg){
            let hasTip = document.getElementById('storeTip')
            if(hasTip){
                hasTip.parentNode.removeChild(hasTip)
            }
            let storeTip = document.createElement('div')
            storeTip.setAttribute('id', 'storeTip')
            storeTip.className = 'store_tip'
            storeTip.innerHTML = `<i class="iconfont icon-k-warn"></i><span>${msg}</span>`
            document.getElementById('app').appendChild(storeTip)

            setTimeout(function(){
                let storeTip = document.getElementById('storeTip')
                storeTip.parentNode.removeChild(storeTip)
            },1200)
        }     
        
    }
}
</script>

<style>
.store_tip{width:1.3rem; height:1.26rem; position:absolute; left:50%; top:50%; margin-top:-.63rem; margin-left:-.65rem; background:rgba(0,0,0,.8); border-radius:.08rem; z-index:1100; color:#EC2E28; text-align:center; padding-top:.2rem;}
.store_tip i{font-size:.46rem;}
.store_tip span{display:block; font-size:.14rem; margin-top:.12rem;}
.goods_detail img{max-width:100%; display:block; pointer-events:none}
</style>

<style scoped>
.goodsMenu{position:fixed; width:2.4rem; left:50%; margin-left:-1.2rem; top:.05rem; z-index:1000;}
.goodsMenu ul{ display:flex;}
.goodsMenu li{flex:1; text-align:center;}
.goodsMenu li span{display:inline-block; height:.43rem; line-height:.43rem; border-bottom:.02rem solid #fff; padding:0 .1rem; font-size:.14rem; color:#333;}
.goodsMenu li.current span{border-color:#333;}

.content{position:absolute; width:100%; height:calc(100% - .92rem); top:.48rem; bottom:.5rem; left:0; z-index:10; overflow-y:auto; -webkit-overflow-scrolling:touch;}

.goods_imgs{width:100%; height:3.12rem;}
.goods_imgs .swiper-container{width:100%; height:3.12rem;}
.goods_imgs img{width:100%; height:100%;}

.goods_info{background:#fff; border-bottom:1px solid #d8d8d8;}
.goods_info h3{font-size:.14rem; color:#333; line-height:.2rem; padding:.08rem .12rem; overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:2;-webkit-box-orient:vertical;}
.goods_box{padding:.04rem .12rem; overflow:hidden; display:flex;}
.goods_box .price{color:#EC2E28; font-size:.24rem; flex:1;}
.goods_box .price em{font-style:normal; font-size:.14rem; margin-right:..06rem; margin-left:-.03rem;}
.goods_box .address{flex:1; text-align:right; color:#999; font-size:.14rem; position:relative; top:.1rem; z-index:1;}
.goods_box .goods_num,
.goods_box .create_time{font-size:.12rem; color:#999; flex:1;}
.goods_box .create_time{text-align:right;}
.goods_license{height:.4rem; width:100%; background:#f8f8f8; margin-top:.06rem; padding:.12rem .15rem;}
.goods_license ul{display:flex;}
.goods_license li{font-size:.12rem; color:#666;}
.goods_license li i{color:#EC2E28; margin-right:.1rem;}
.goods_license li span{margin-right:.4rem; position:relative; top:-.01rem;}

.goods_attrs{background:#fff; margin:.11rem 0; padding:.14rem .1rem; border-top:1px solid #d8d8d8; border-bottom:1px solid #d8d8d8; font-size:.16rem; color:#333;}
.goods_attrs i{float:right; color:#CDCED2; position:relative; top:.03rem;}

.goods_store{background:#fff; margin-top:.14rem; padding:.12rem; overflow:hidden;}
.store_info{display:flex;}
.store_info img{width:.4rem; height:.4rem;}
.store_info h3{flex:1; font-size:.14rem; color:#333; padding-left:.18rem;}
.store_info .stars{margin-left:.06rem; position:relative; top:.01rem;}
.to_shop{display:block; margin:.15rem auto; width:.8rem; height:.24rem; line-height: .22rem; background:#fff; font-size:.12rem; color:#666; text-align:center; border:1px solid #999; border-radius:.04rem}

.detail_more{margin:.1rem 0; padding:.1rem 0; font-size:.14rem; color:#999; text-align:center; position:relative;}
.detail_more span{display:inline-block; padding:0 .1rem; background:#f2f2f2;}
.detail_more:before{content:""; width:3.25rem; height:1px; background:#d8d8d8; position:absolute; top:50%; left:50%; margin-left:-1.64rem; z-index:-1;}

.goods_detail, .goods_attrbox{background:#fff; position:absolute; width:100%; height:100%; left:0; top:0; overflow-y:auto;}
.goods_detail .to_top{position:fixed; right:.15rem; bottom:.8rem; width:.4rem; height:.4rem; line-height:.42rem; background:rgba(236, 46,40,.6); color:#fff; border-radius:50% 50%; text-align:center;}
.goods_detail .to_top i{font-size:.2rem;}

.goods_attrbox{padding:0 .08rem .08rem;}
.goods_attrbox li{border-bottom:1px solid #d8d8d8; padding:.15rem 0; display:flex; font-size:.12rem; color:#333;}
.goods_attrbox li label{width:1.5rem; padding-left:.06rem;}
.goods_attrbox li span{flex:1;}

.foot{position:fixed; bottom:0; left:0; width:100%; height:.5rem; line-height:.5rem; background:#fff; display:flex; z-index:1130;}
.foot span{display:block; flex:1; font-size:.14rem; text-align:center;}
.foot .store{box-shadow:0 -1px 2px 0 rgba(0,0,0,.1); color:#333;}
.foot .store i{font-size:.16rem; margin-right:.04rem; position:relative; top:.01rem; font-weight:bold;}
.foot .stored i{color:#EC2E28;}
.foot .upload_taobao{background:#EC2E28; color:#fff;}

.sdk_shade{position:fixed; width:100%; height:100%; top:0; left:0; background:rgba(0,0,0,.3); z-index:1140;}
.sdk_layer{position:absolute; width:100%; height:5rem; bottom:0; left:0; background:#fff; z-index:1150;}
.sdk_layer .icon-close{position:absolute; right:.12rem; top:.12rem; font-size:.18rem; color:#999;}
.sdk_layer .goods_baseinfo{height:1.1rem; border-bottom:1px solid #d8d8d8; padding:0 .15rem; display:flex;}
.sdk_layer .goods_baseinfo img{width:1.2rem; height:1.2rem; position:relative; top:-.33rem; box-shadow:0 0 .05rem rgba(0,0,0,.2); border-radius:.04rem;}
.sdk_layer .goods_basebox{flex:1; margin-left:.12rem; margin-top:.2rem;}
.sdk_layer .goods_basebox span{display:block; padding:.05rem 0;}
.sdk_layer .goods_basebox .price{color:#ec2e28; font-size:.16rem;}
.sdk_layer .goods_basebox .price em{font-size:.14rem; margin-right:.04rem; font-style:normal;}
.sdk_layer .goods_basebox .goods_num{font-size:.14rem; color:#333;}
.sdk_layer .sdk_list{max-height:3.75rem; overflow-y:auto; -webkit-overflow-scrolling:touch;}
.sdk_layer .sdk_item{margin:.12rem .15rem 0; border-bottom:1px solid #d8d8d8; padding:.06rem 0 .22rem 0;}
.sdk_layer .sdk_item:last-child{border-bottom:0;}
.sdk_layer .sdk_item h3{font-size:.14rem; color:#333;}
.sdk_layer .sdk_item ul{overflow:hidden;}
.sdk_layer .sdk_item li{padding:0 .2rem; height:.28rem; line-height:.28rem; background:#f2f2f2; border-radius:2px; float:left; margin:.12rem .12rem 0 0; font-size:.14rem; color:#666; text-align:center;}
.sdk_layer .sdk_size li{width:.68rem; padding:0;}

</style>



