//for all goodsList
export const querySearchGoods = async (options) => {
    var res = await fetch('/datas/queryGoodsList.json', options)
    if(res.success){
        res.goodsList = res.items.map((goods) => ({
            goodsid: goods.goodsId,
            title: goods.title,
            price: goods.piprice,
            market: goods.fullStoreName,
            imgSrc: goods.imgsrc,
        }))
    }
    return res
}

