<template>
  	<div>
        <div class="content">
            <div class="goods_attrbox">
                <ul>
                    <li v-for="attr in itemKvs"><label>{{attr.key}}</label><span>{{attr.value}}</span></li>
                </ul>
            </div>

            <div class="goods_detail" @touchmove="showTopIcon">
                <div v-html="details">{{details}}</div>
                <div class="to_top" v-if="isScroll" @click="gotoTop">
                    <i class="iconfont icon-upload"></i>
                </div>
            </div>
        </div>        
    </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import{getGoodsDetail, ajax} from '../data/service'

export default {
    data(){
    	return{
            isScroll: false, //详情scrollTop>0
            website: this.$route.query.webSite,
            goodsId: this.$route.query.goodsId,            
            itemKvs: [],
            details: '',
    	}
    },  
    created(){
        this.getGoodsInfo()
        
    },
    mounted(){
    },
    mixins:[ajax],
    methods:{       
        //获取商品数据
        async getGoodsInfo(){
            this.ajax(await getGoodsDetail(this.website, this.goodsId), function(resp){
                this.itemKvs = resp.itemKvs
                this.details = resp.details
            })
        },
        //返回顶部
        gotoTop(){
            document.getElementById('goodsDetail').scrollTop = 0
            this.isScroll = false
        },
        //显示返回顶部按钮
        showTopIcon(){
            let top = document.getElementById('goodsDetail').scrollTop
            if(top > 0){
                this.isScroll = true
            }else{
                this.isScroll = false
            }
        },
        
    }
}
</script>

<style>
.goods_detail img{max-width:100%; pointer-events:none}
</style>

<style scoped>
.goods_detail, .goods_attrbox{background:#fff; width:100%;}
.goods_detail .to_top{position:fixed; right:.15rem; bottom:.8rem; width:.4rem; height:.4rem; line-height:.42rem; background:rgba(236, 46,40,.6); color:#fff; border-radius:50% 50%; text-align:center;}
.goods_detail .to_top i{font-size:.2rem;}

.goods_attrbox{padding:0 .08rem .08rem;}
.goods_attrbox li{border-bottom:1px solid #d8d8d8; padding:.15rem 0; display:flex; font-size:.12rem; color:#333;}
.goods_attrbox li label{width:1.5rem; padding-left:.06rem;}
.goods_attrbox li span{flex:1;}
</style>



