<template>
	<div class="bg_white">
		<common-head :view="{
			goback: true,
			pageTitle: '修改密码'
		}"></common-head>
		<ul class="modify_form">
			<li><input v-model="oldPwd" type="password" placeholder="旧密码" name="oldPwd" maxlength="20"></li>
			<li><input v-model="newPwd" type="password" placeholder="新密码" name="newPwd" maxlength="20"></li>
			<li><input v-model="pwdAgain" type="password" placeholder="确认密码" name="passwordAgain" maxlength="20"></li>
		</ul>
		<span class="modify_btn" @click="updatePwd">确认修改</span>

	</div>
</template>

<script>
import commonHead from '../components/commonHead'
import {msgTip} from '../plugins/util'
import {modifyPassword, ajax} from '../data/service'
export default{
	data(){
		return {
			oldPwd:'',
			newPwd: '',
			pwdAgain: ''
		}
	},
	components:{
		commonHead
	},
	mixins:[ajax],
	methods:{
		async modifyPwd(){
			this.ajax(await modifyPassword(this.oldPwd, this.newPwd), function(resp){
				msgTip('修改成功')
			})
		},
		updatePwd(){
			if(!this.oldPwd){
				return msgTip('旧密码不能为空')
			}else if(!this.newPwd){
				return msgTip('新密码不能为空')
			}else if(!this.pwdAgain){
				return msgTip('确认密码不能为空')
			}else if(this.newPwd != this.pwdAgain){
				return msgTip('两次密码不一致')
			}else{
				this.modifyPwd()
			}
		}
	}

}
</script>

<style scoped>
.bg_white{background:#fff; position:absolute; width:100%; height:100%; top:0; left:0;}
.modify_form{padding:.7rem .23rem 0;}
.modify_form li{border-bottom:1px solid #d8d8d8; padding:.05rem 0;}
.modify_form li input{width:100%; height:.4rem; line-height:.4rem; font-size:.14rem;}
.modify_btn{display:block; margin:.2rem .23rem; height:.5rem; line-height:.5rem; color:#fff; font-size:.16rem; text-align:center; background:#198ADB; border-radius:.04rem;}
</style>


