<template>
	<div>
		<common-head class="head_top" :view="{
			goback:true,
			searchBar: 2
		}"></common-head>
		
		<!--<div class="search_history" v-if="!keyword" id="searchHistory">
			<ul>
				<li v-for="(item, index) in history" v-if="index < 5">
					<i class="iconfont icon-clock"></i>
					{{item}}
					<i class="iconfont icon-close" :id="'close'+index" @click="deleteItem(index)"></i>
				</li>
			</ul>
			<div class="delete_btn" @click="deleteAll">清除历史搜索</div>	
		</div>-->

		<div>
			<div class="goods" v-if="searchType == 'goods'">				
				<div class="menu">
					<ul>
						<li v-for="item in menus" @click="orderBy(item.type)" :class="{current: orderType == item.type}">
							<span>{{item.name}}</span>
						</li>

						<li @click="orderBy('price-desc')" :class="{current: priceType == 'price-desc'}" v-if="this.priceType == 'price-desc'"><span>价格</span></li>
						<li @click="orderBy('price-asc')" :class="{current: priceType == 'price-asc'}" v-else><span>价格</span></li>
						
					</ul>
				</div>
				<div class="no_data" v-if="goodsList.length == 0">暂无数据</div>
				<goods-list class="goods_list" :goods="goodsList" v-else></goods-list>
			</div>

			<div class="shops" v-else-if="searchType == 'shop'">
				<div class="no_data" v-if="shopList.length == 0">暂无数据</div>
				<ul v-else>
					<li v-for="item in shopList">
			            <router-link :to="{name:'shop', params:{id:item.shopId}}">
			                <img :src="item.shopHeadUrl">                    
			                <div class="info">
			                    <h3>{{item.market}}{{item.shopNum}}</h3>
			                    <star-num :stars="item.starNum"  class="stars"></star-num>
			                    <span class="main_case">主营：{{item.mainCase}}</span>
			                </div>
			            </router-link>
						<div class="contact">
							<i class="iconfont icon-phone" @click="phoneCall(item.telephone)"></i>
							<span>共{{item.itemNum}}件宝贝</span>
						</div>
					</li>
				</ul>
			</div>
		</div>

		<div v-show="goodsHasNext && searchType == 'goods'" @click="goodsNextPage" class="next_page"><span>点击加载更多</span></div>
		<div v-show="shopHasNext && searchType == 'shop'" @click="shopNextPage" class="next_page"><span>点击加载更多</span></div>
		
		<loading></loading>

	</div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
import commonHead from '../components/commonHead'
import goodsList from '../components/goodsList'
import starNum from '../components/stars'
import Loading from '../components/loading'
import {querySearchGoods, querySearchShop, ajax} from '../data/service'

export default{
	data(){
		return{
			//history: ['男装T恤', '电商基地', '秋装连衣裙', '格子衬衫', '钱塘大厦111', '你好好好好'],
			menus: [
				{type:'common', name:'综合'},
				{type:'xp', name:'新品'},
				{type:'popular', name:'人气'},
			],
			orderType: 'common',
			searchType: this.$route.query.type,
			keyword: this.$route.query.keywords,	
			goodsList: [],
            goodsIndex: 1,
            goodsHasNext: false,
            goodsRerender: true,
			shopList: [],
			shopIndex: 1,
			shopHasNext: false,
			shopRerender: true,
			priceType: '',
		}
	},
	components:{
		commonHead,
		goodsList,
		starNum,
		Loading,
	},
	mounted(){
		if(this.searchType == 'goods' && this.keyword){
			this.categoryGoods()
		}else if(this.searchType == 'shop' && this.keyword){
			this.searchShop()
		}
		
	},
	mixins:[ajax],
	computed:{
		...mapState({
			'website': state => state.website,
			'loading': state => state.loading,
		})
	},
	methods:{
		...mapActions(['startLoding']),

	    loadingShow(){
	        this.startLoding({status:true})       
	    },
	    loadingHide(){
	        this.startLoding({status:false})
	    },
		//获取商品列表
		async categoryGoods(){
			this.loadingShow()

			let options = {
				webSite: this.website,
				keyword: this.keyword,
				orderBy: this.orderType,
				index: this.goodsIndex,
				size: 30,
				type: 2,
			}
			this.ajax(await querySearchGoods(options), (res) => {				
            	if(res.goodsList.length == 0){
            		this.goodsList = []
            		this.goodsHasNext = false            		
            	}else{
	            	if(this.goodsRerender){ 
	                	this.goodsRerender = false
	                	this.goodsList = res.goodsList                	
	                }else{
	                	this.goodsList = this.goodsList.concat(res.goodsList)
	                }
	                this.goodsHasNext = res.hasNext	                
	            }	            
            })
            this.loadingHide()
            
		},
		//获取店铺列表
		async searchShop(){
			this.loadingShow()

			let options = {
				webSite: this.website,
				keyword: this.keyword,
				index: this.shopIndex,
				size: 30,
			}
			this.ajax(await querySearchShop(options), (res) => {
				if(res.shopList.length == 0){
					this.shopList = []
					this.shopHasNext = false
				}else{
					if(this.shopRerender){
						this.shopRerender = false
						this.shopList = res.shopList
					}else{
						this.shopList = this.shopList.concat(res.shopList)
					}
					this.shopHasNext = res.hasNext									
				}			
			})
			this.loadingHide()				
		},
		//获取当前菜单
		orderBy(type){
			if(type == 'price-asc'){
				this.priceType = 'price-desc'
			}else if(type == 'price-desc'){
				this.priceType = 'price-asc'
			}else{
				this.priceType = ''
			}
			this.orderType = type			
			this.categoryGoods()
		},
		//加载更多商品
		goodsNextPage(){
			this.goodsIndex++
			this.categoryGoods()
		},
		//加载更多店铺
		shopNextPage(){
			this.shopIndex++
			this.searchShop()
		},

		/*deleteItem(id){
		    var historyItem = document.getElementById('close'+id);
		    var parenLi = historyItem.parentNode;
		    parenLi.parentNode.removeChild(parenLi);
		    var historyList = document.getElementById('searchHistory');
		    var liLength = historyList.getElementsByTagName('li').length
		    if(liLength == 0){
		    	historyList.parentNode.removeChild(historyList);
		    }
		},
		deleteAll(){
			var historyList = document.getElementById('searchHistory');
			historyList.parentNode.removeChild(historyList);
		},*/
		changeMenu(item){
			this.currentMenu = item
		}
	},
    watch:{
        $route(){
            this.searchType = this.$route.query.type
            this.keyword = this.$route.query.keywords
            if(this.searchType == 'goods' && this.keyword){
            	this.goodsRerender = true
                this.categoryGoods()
            }else if(this.searchType == 'shop' && this.keyword){
            	this.shopRerender = true
                this.searchShop()
            }           
        }
    }
}
</script>

<style>
.head_top .search-type{width:2.8rem; height:.34rem; position:absolute; right:.12rem; top:.08rem; background:#f2f2f2; border-radius:.08rem;}	
</style>

<style scoped>
.head_top{padding:0 .12rem;}

.search_history{padding-top:.48rem;}
.search_history li{ padding:.14rem .16rem; overflow:hidden; font-size:.14rem; color:#999; border-bottom:1px solid #d8d8d8;}
.search_history li i{position:relative; top:.01rem;}
.search_history li .icon-clock{font-size:.18rem; margin-right:.06rem;}
.search_history li .icon-close{float:right; top:.03rem;}

.delete_btn{width:3.5rem; height:.4rem; line-height:.4rem; text-align:center; color:#999; font-size:.14rem; background:#fff; border:1px solid #d8d8d8; border-radius:.03rem; margin:.14rem auto;}

.goods,
.shops{width:100%; margin-top:.48rem; background:#fff;}
.goods .menu{width:100%; height:.48rem; padding-top:.04rem; background:#fff; position:fixed; top:.46rem; left:0; box-shadow:0 1px .06rem rgba(0,0,0,.1); z-index:10;}
.goods .menu ul{display:flex; text-align:center;}
.goods .menu li{flex:1; font-size:.15rem;}
.goods .menu li span{display:inline-block; padding:0 .08rem; height:.44rem; line-height:.44rem; border-bottom:.02rem solid #fff;}
.goods .menu li.current span{border-color:#333;}
.goods .goods_list{padding-top:.48rem;}

.shops{padding-bottom:.14rem;}
.shops li{padding:.14rem .06rem .14rem 0; margin:0 .12rem; border-bottom:1px solid #d8d8d8; overflow:hidden; display:flex;}
.shops a{flex:1; display:inline-flex;}
.shops li img{width:.62rem; height:.62rem; margin-right:.1rem;}
.shops li .info{position:relative; height:.62rem; overflow:hidden;}
.shops li .info h3{width:1.8rem; font-size:.14rem; color:#333; overflow:hidden; white-space:nowrap; text-overflow:ellipsis;}
.shops li .info .stars{margin-top:-.02rem;}
.shops li .info .main_case{position:absolute; font-size:.12rem; color:#999; left:0; bottom:0;}
.shops li .contact{width:.7rem; text-align:center;}
.shops li .contact i{font-size:.3rem; color:#333; display:block; margin-top:.06rem;}
.shops li .contact span{display:block; color:#999; font-size:.12rem; margin-top:.04rem;}
</style>


