<template>
    <div class="bqsqCon">
        <div class="headImg">
            <img src="../assets/appBqsqBanner.png">
        </div>
        <div class="writeBqsqCon">
            <h2>填写信息</h2>
            <div class="writeCon">
                <ul>
                    <li>
                        <label>类型选择：</label>
                        <select :value="type" v-model="type">
                            <option disabled value="">请您选择要注册的类型</option>
                            <option value="1">普通注册</option>
                            <option value="2">加急注册</option>
                            <option value="3">担保注册</option>
                            <option value="4">双享注册</option>
                            <option value="5">普通登记</option>
                            <option value="6">加急登记</option>
                            <option value="7">外观专利</option>
                            <option value="8">设计稿作品</option>
                        </select>
                    </li>
                    <li>
                        <label>姓名：</label>
                        <input type="text" placeholder="请您输入您的姓名" v-model="userName">
                    </li>
                    <li>
                        <label>手机号码：</label>
                        <input type="text" placeholder="请您输入您的手机号码" v-model="userTele">
                    </li>
                    <li class="otherCs">
                        <label>要求说明：</label>
                        <textarea placeholder="请您输入您的说明" v-model="askFor">{{askFor}}</textarea>
                    </li>
                    <li>
                        <button class="submitInfoBtn" @click="submitInfo()">确认提交</button>
                    </li>
                </ul>
            </div>
        </div>

        <div class="trademark markConCss">
            <div class="registTitle">
                <img src="../assets/sbTip.png">
            </div>
            <div class="trademarkCon">
                <ul class="clearfix">
                    <li v-for="trademark in trademarkList">
                        <a :href="trademark.href">
                            <img :src="trademark.imgSrc">
                            <div class="serverFee">
                                <p class="projectName">{{trademark.name}}</p>
                                <p>{{trademark.price}}元/件</p>
                            </div>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="copyright markConCss">
            <div class="registTitle">
                <img src="../assets/bqTip.png">
            </div>
            <div class="copyrightCon">
                <ul class="clearfix">
                    <li v-for="copyright in copyrightList">
                        <a :href="copyright.href">
                            <img :src="copyright.imgSrc">
                            <div class="serverFee">
                                <p class="projectName">{{copyright.name}}</p>
                                <p>{{copyright.price}}元/件</p>
                            </div>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="successCase">
            <div class="registTitle">
                <img src="../assets/cgTip.png">
            </div>
            <div class="successCaseCon">
                <div class="case1">
                    <img src="../assets/case1.jpg">
                </div>
                <div class="case2">
                    <img src="../assets/case2.jpg">
                </div>
                <div class="case3">
                    <img src="../assets/case3.jpg">
                </div>
                <div class="case4">
                    <img src="../assets/case4.jpg">
                </div>
            </div>
        </div>

        <div class="contact" @click="registCall">客服电话：400-076-1116</div>
        
        <div class="floatingWindow" v-if="showSuccess">
            <div class="layerShap">
                <div class="succesCon">
                    <div class="clearfix closeOpe"><a href="javascript:;" @click="closeWindow()"><img class="closeIcon" src="../assets/closeIcon.png"></a></div>
                    <div class="tipCon">
                        <div class="successIcon"><img src="../assets/successIcon.png"></div>
                        <p class="first">您的资料已提交成功！</p>
                        <p class="last">客服人员会在24小时之内及时联系您<br>请耐心等待~</p>
                    </div>
                </div>
            </div>
        </div>
    </div>    
</template>

<script>
import {mapState, mapActions} from 'vuex'
import {submitRegeditInfo, ajax} from '@/data/service'
import {msgTip} from '@/plugins/util'
import trademark1 from '@/assets/trademark1.jpg'
import trademark2 from '@/assets/trademark2.jpg'
import trademark3 from '@/assets/trademark3.jpg'
import trademark4 from '@/assets/trademark4.jpg'
import copyright1 from '@/assets/copyright1.jpg'
import copyright2 from '@/assets/copyright2.jpg'
import copyright3 from '@/assets/copyright3.jpg'
import copyright4 from '@/assets/copyright4.jpg'


export default {
    data(){
        return {
            trademarkList:[
                {name:'普通注册', price:'1288', imgSrc:trademark1, href:'https://item.taobao.com/item.htm?spm=a1z10.3-c.w4002-13309679213.9.6403c6c4dAGPor&id=559970936103'},
                {name:'加急注册', price:'1398', imgSrc:trademark2, href:'https://item.taobao.com/item.htm?spm=a1z10.3-c.w4002-13309679213.15.6403c6c4dAGPor&id=560064777629'},
                {name:'担保注册', price:'1680', imgSrc:trademark3, href:'https://item.taobao.com/item.htm?spm=a1z10.3-c.w4002-13309679213.24.6403c6c4dAGPor&id=560127246264'},
                {name:'双享注册', price:'1600', imgSrc:trademark4, href:'https://item.taobao.com/item.htm?spm=a1z10.3-c.w4002-13309679213.27.6403c6c4dAGPor&id=560127986051'},
            ],
            copyrightList:[
                {name:'普通登记', price:'800', imgSrc:copyright1, href:'https://item.taobao.com/item.htm?spm=a1z10.3-c.w4002-13309679213.12.6403c6c4dAGPor&id=559975012669'},
                {name:'加急登记', price:'1000', imgSrc:copyright2, href:'https://item.taobao.com/item.htm?spm=a1z10.3-c.w4002-13309679213.30.6403c6c4dAGPor&id=560129154701'},
                {name:'外观专利', price:'1300', imgSrc:copyright3, href:'https://item.taobao.com/item.htm?spm=a1z10.3-c.w4002-13309679213.21.6403c6c4dAGPor&id=560068845596'},
                {name:'设计稿作品', price:'898', imgSrc:copyright4, href:'https://item.taobao.com/item.htm?spm=a1z10.3-c.w4002-13309679213.18.6403c6c4dAGPor&id=560068557887'},
            ],
            showSuccess: false,
            askFor:'',
            userName: '',
            userTele: '',
            type: '',
            token: this.$route.query.token
        }
    },
    mixins: [ajax],
    computed:{
        ...mapState(['userId', 'userType']),        
    },
    methods: {
        ...mapActions(['initUserLoginState']),
        async submitInfo(){
            if(!this.token){
                await this.initUserLoginState()

                if(!this.userId){
                    this.$router.push('/login')
                }else if(this.userType == 2){
                    return msgTip('供应商不支持此操作', '我知道了')
                }else{
                    this.validateForm()
                }
            }else{
                this.validateForm()
            } 
        },
        validateForm(){
            if(!this.type){
                return msgTip('请选择注册类型'); 
            }else if(!this.userName){
                return msgTip('请填写您的姓名');
            }else if(!this.userTele){
                return msgTip('请填写您的手机号码');
            }else if(!/^(86-)?1(3|4|5|7|8)\d{9}$/.test(this.userTele)){
                return msgTip('请填写正确的手机号码');
            }else{
                this.regeditInfoSet()
            }
        },
        closeWindow(){
            this.showSuccess = false;
        },

        async regeditInfoSet(){
            let options = {
                type: this.type,
                userName: this.userName,
                userTele: this.userTele,
                askFor: this.askFor,
                token: this.token ? this.token : ''
            }
            this.ajax(await submitRegeditInfo(options), function(resp){
                this.showSuccess = true;
                this.type = '';
            })
        },
        registCall(){
            window.location.href = 'tel:400-076-1116'
        }
    },

}
</script>

<style scope>
.bqsqCon{width:100%; font-size: .2rem}
.bqsqCon .headImg{display: inline-block; width:100%;}
.bqsqCon .headImg img{width:100%;}

.bqsqCon .writeBqsqCon{}
.bqsqCon .writeBqsqCon h2{width:100%; text-align: center; font-weight: normal; font-size: .2rem; padding:.1rem 0 .14rem;}
.bqsqCon .writeBqsqCon .writeCon{padding-left:.15rem; padding-right: .15rem}
.bqsqCon .writeBqsqCon .writeCon li{background: #fff; border:.01rem solid #e6e6e6; line-height:.39rem; border-radius:0.02rem; margin-bottom: .14rem;}
.bqsqCon .writeBqsqCon .writeCon li label{font-size:.14rem; display: inline-block; width: .72rem; padding-left: 0.2rem;}
.bqsqCon .writeBqsqCon .writeCon li input[type=text]{width:2.3rem; height:.3rem; box-sizing: none; font-size: .12rem; outline:none;}
.bqsqCon .writeBqsqCon .writeCon li select{width:2.3rem; height:.3rem; font-size: .12rem; outline:none; background-color:transparent; border-color:transparent; -webkit-appearance: none;}
.bqsqCon .writeBqsqCon .writeCon li select option{font-size: .12rem;}
.bqsqCon .writeBqsqCon .writeCon li.otherCs{overflow: hidden;}
.bqsqCon .writeBqsqCon .writeCon li textarea{width:2.3rem; height:1.48rem; font-size: .12rem; outline:none; float:left; padding: .10rem .08rem;}
.bqsqCon .writeBqsqCon .writeCon li.otherCs label{float:left;}
.bqsqCon .writeBqsqCon .writeCon li .submitInfoBtn{width:100%; background:#ff7f00; color:#fff; height:.4rem; font-size: .2rem}

.bqsqCon .registTitle{text-align:center; margin:.05rem 0 .14rem;}
.bqsqCon .registTitle img{width:1.76rem;}

.bqsqCon .trademarkCon,
.bqsqCon .copyrightCon,
.bqsqCon .successCaseCon{padding-left:.15rem;}

.bqsqCon .markConCss ul{overflow:hidden;}

.bqsqCon .markConCss li{float:left; margin-right:.15rem; margin-bottom:.22rem;}

.bqsqCon .markConCss li img{width:1.65rem;}

.bqsqCon .markConCss .serverFee{background:#fff; font-size:.14rem; text-align:center; border:.01rem solid #d4d4d4; margin-top: -.05rem; font-size: .12rem; padding:.1rem 0 .08rem; color:#666;}
.bqsqCon .markConCss .serverFee .projectName{font-size: .14rem; color:#333; margin-bottom: .04rem}

.bqsqCon .successCase{margin-bottom:.14rem;}
.bqsqCon .successCase .case1 img,
.bqsqCon .successCase .case2 img{width:3.45rem;}

.bqsqCon .successCase .case3{margin-right:.14rem; float:left;}
.bqsqCon .successCase .case3 img,
.bqsqCon .successCase .case4 img{width:1.65rem;}

.bqsqCon .contact{background:#fff; border:1px solid #848484; text-align:center; line-height:.36rem; margin:.4rem .15rem .5rem .15rem; font-size:.14rem; color:#404040;}

.bqsqCon .floatingWindow{position: fixed; top:0; left:0; width:100%; height:100%; background:rgba(0,0,0,.2); z-index: 99}
.bqsqCon .floatingWindow .layerShap{position:relative; width:2.6rem; height:1.7rem; background:#fff; top:50%; left:50%; margin-left:-1.3rem; margin-top:-.85rem;}
.bqsqCon .floatingWindow .layerShap .succesCon{border-top:.02rem solid #00a0e9;}

.bqsqCon .floatingWindow .layerShap .succesCon .closeOpe{overflow:hidden;}
.bqsqCon .floatingWindow .layerShap .succesCon .closeIcon{float:right; width:.2rem; margin:.07rem .07rem 0 0;}

.bqsqCon .floatingWindow .tipCon{text-align:center; padding:0 .2rem; font-size: .14rem; color:#666;}
.bqsqCon .floatingWindow .tipCon .successIcon{text-align:center;}
.bqsqCon .floatingWindow .tipCon .successIcon img{width:.4rem;}
.bqsqCon .floatingWindow .tipCon p{margin-top:.08rem;}
.bqsqCon .floatingWindow .tipCon p.first{font-size:.18rem; color:#333;}
.bqsqCon .floatingWindow .tipCon p.last{font-size: .12rem;}
</style>




