<template>
  	<div v-if="showTemp" class="tb_temp">
        <common-head :view="{
            pageTitle: '运费模板'
        }"></common-head>  
        
        <go-back @backFormChild="backToParent('postTemp')"></go-back>
        <span class="updata_temp" @click="updateTbTemp">更新模板</span>

        <section class="set_list" v-if="templates.length > 0">
            <ul>
                <li v-for="temp in templates" @click="tempToParent(temp.tid, temp.name)"><span>{{temp.name}}</span><em class="iconfont icon-right"></em></li>        
            </ul>
        </section>
        <section class="no_item" v-else>
            <span></span>
            <p>没有找到您淘宝店铺的运费模板<br>别慌，更新一下模板试试~</p>
        </section>

    </div>
</template>

<script>
import commonHead from '../components/commonHead'
import goBack from '../page/uploadTbGoBack'
import {getTbPostTemp, ajax} from '../data/service'

export default {
    data: function(){
    	return{
            templates:''
    	}
    },
    created(){
        this.getTbTemps()
    },
    mixins:[ajax],
    methods:{
        async getTbTemps(){
            this.ajax(await getTbPostTemp(), function( reps){
                this.templates = reps.templates              
            })
        },
        //更新模板
        updateTbTemp(){
            this.getTbTemps()
        },
        //向父组件传选中的值
        tempToParent(id, item){
            this.$emit('valueFromTempChild', {id, item})
        },
        backToParent(data){
            this.$emit('hideThisChild', data)
        }
    },
    components:{
        commonHead,
        goBack
    },
    props:['showTemp']
}
</script>

<style scoped>
.tb_temp{position:absolute; background:#f2f2f2; z-index:110; width:100%; height:100%; left:0; top:0;}
.updata_temp{position:fixed; font-size:.14rem; color:#333; top:.14rem; right:.12rem; z-index:200;}

.no_item{position:absolute; width:2rem; height:1.9rem; top:50%; left:50%; margin-left:-1rem; margin-top:-.95rem; }
.no_item span{display:block; width:.97rem; height:.75rem; background:url(../assets/noItem.png) no-repeat 0 0 / 100% 100%; margin:0 auto;}
.no_item p{font-size:.14rem; color:#d8d8d8; line-height:.32rem; margin-top:.1rem; text-align:center;}

.set_list{margin-top:.56rem; background:#fff}
.set_list ul{margin-top:.15rem; background:#fff; padding:0 .14rem;}
.set_list li{border-bottom:1px solid #d8d8d8; padding:.1rem 0; font-size:.16rem; color:#333; overflow:hidden;}
.set_list li span{line-height:.32rem;}
.set_list li em{float:right; color:#d8d8d8; font-size:.2rem; position:relative; top:.03rem; right:-.04rem;}
.set_list ul li:last-child{border-bottom:0;}
</style>



