<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '登录',
			registEntry: true
		}"></common-head>
		<div class="container">
			<div class="form">
				<ul>
					<li><input v-model="user" type="text" name="user" placeholder="手机号/账号"></li>
					<li>
						<input v-model="pwd" type="password" name="password" placeholder="密码">
						<router-link to="/forgetPassword">忘记密码</router-link>
					</li>
					<li><div class="login_btn" @click="login">登录</div></li>
				</ul>
			</div>
			<div class="msg_login"><router-link to="/msgLogin">手机短信验证码登录</router-link></div>
			<div class="other_login">
				<ul>
					<li><a href="https://oauth.m.taobao.com/authorize?response_type=code&client_id=21720662&redirect_uri=http://www.571xz.net/redirect_auth.jsp&state=h5otherLoginTest&view=wap"><i class="iconfont icon-taobao"></i><span>淘宝登录</span></a></li>
					<!--<li><a href="https://oauth.m.taobao.com/authorize?response_type=code&client_id=21720662&redirect_uri=http://www.571xz.net/redirect_auth.jsp&state=h5otherLogin&view=wap"><i class="iconfont icon-taobao"></i><span>淘宝登录</span></a></li>-->
					<!--<li><router-link :to="{path:'/bindTelephone', query:{type:2}}"><i class="iconfont icon-weixin"></i><span>微信登录</span></router-link></li>-->
					<li><i class="iconfont icon-weixin"></i><span>微信登录</span></li>
				</ul>
			</div>
		</div>

	</div>
</template>

<script>
import commonHead from '../components/commonHead'
import {msgTip} from '../plugins/util'
import {login, ajax} from '../data/service'
export default{
	data(){
		return{
			user:'',
			pwd:''
		}
	},
	components:{
		commonHead
	},
	mixins:[ajax],
	methods:{
		//登录接口
		async userLogin(){
			this.ajax(await login(this.user, this.pwd), function(resp){
				this.$router.push('/home')
			})
		},
		login(){
			if(!this.user){
				return  msgTip('用户名不能为空')				
			}else if(!this.pwd){
				return msgTip('密码不能为空')				
			}else{				
				this.userLogin()
			}
		}
	}
}
</script>

<style scoped>
.container{background:#fff; position:absolute; top:.48rem; left:0; width:100%; height: calc(100% - .48rem);}
.form{padding:.12rem .15rem 0 .15rem;}
.form li{position:relative; margin-top:.12rem; overflow:hidden; border-bottom:1px solid #d8d8d8;}
.form li input{width:100%; height:.4rem; line-height:.4rem; font-size:.16rem;}
.form li a{position:absolute; right:.12rem; font-size:.14rem; color:#666; top:.1rem;}
.form li:last-child{border-bottom:0;}
.form li .login_btn{height:.44rem; line-height:.44rem; margin-top:.2rem; color:#fff; font-size:.16rem; text-align:center; background:#0688DE; border-radius:.04rem;}

.msg_login{text-align:center;  margin:.26rem auto;}
.msg_login a{color:#0688DE; font-size:.14rem;}

.other_login{padding:0 .3rem;}
.other_login ul{display:flex;}
.other_login li{text-align:center; flex:1;}
.other_login i{font-size:.56rem;}
.other_login .icon-taobao{color:#f40;}
.other_login .icon-weixin{color:#5de466;}
.other_login li span{display:block; font-size:.12rem; color:#666;}
</style>


